import { Box, Typography, useTheme } from "@mui/material";
import { tokens } from "../../theme";
import Grid from "@mui/material/Grid";
import url from "../url"
import axios from "axios"
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
import { useLocation } from 'react-router-dom';
import PersonIcon from '@mui/icons-material/Person';
import Avatar from '@mui/material/Avatar';
import React, { useState, useEffect } from "react";
import List from '@mui/material/List';
import ListItemText from '@mui/material/ListItemText';
import ListItemButton from '@mui/material/ListItemButton';
import Modal from '@mui/material/Modal';
import FavoriteIcon from '@mui/icons-material/Favorite';
import HeartBrokenIcon from '@mui/icons-material/HeartBroken';
import Fade from '@mui/material/Fade';
import Backdrop from '@mui/material/Backdrop';

import {
  DataGrid,
} from '@mui/x-data-grid';

const style = {
  position: 'absolute',
  overflowY: 'scroll',
  height: '550px',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  // width: '100%',
  bgcolor: 'beige',
  borderRadius: '10px',
  padding: "0px 40px 40px 40px",
  boxShadow: 24,
  // p: 4,
};
const CustomerView = () => {
  const { state } = useLocation();
  const [type_of_customer, settype_of_customer] = useState('')
  const [name, setname] = useState('')
  const [dataOrders, setdataOrders] = useState([])
  const [dataChats, setdataChats] = useState([])


  const getAllData = () => {

    axios.get(`${url}api/user/get-user-by-ID/${state.idData}`)
      .then((response) => {
        console.log(response.data.result[0])
        if (response.data.result[0].verified_status) {
          settype_of_customer('Verified profile')
        } else {
          settype_of_customer('Not Verified')

        }
        setname(response.data.result[0].email)
        setLoading(false)
      })
      .catch(error => console.error(`Error:${error}`));
  }
  const getAllDataSP = () => {

    axios.post(`${url}api/UserSubscription/getByUserId`, {
      userId: state.idData
    })
      .then((response) => {
        console.log(response.data.result)
        setdataOrders(response.data.result)
      })
      .catch(error => console.error(`Error:${error}`));
  }
  const getAllDataSPChat = () => {

    axios.post(`${url}api/room/get-room-by-user-id`, {
      user_id: state.idData
    })
      .then((response) => {
        console.log(response.data.result)
        setdataChats(response.data.result)
      })
      .catch(error => console.error(`Error:${error}`));
  }
  useEffect(() => {
    getAllData();
    getAllDataSP();
    getAllDataSPChat();
    // eslint-disable-next-line
  }, []);
  const theme = useTheme();

  const colors = tokens(theme.palette.mode);

  const [loading, setLoading] = useState(true)
  const columns = [

    {
      field: "subscriptionPlan",
      headerName: "subscriptionPlan",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            {(row.row.subscriptionPlan === undefined) || (row.row.subscriptionPlan === null) ? <>
              Null
            </> :
              <>
                {row.row.subscriptionPlan.title}
              </>}
          </>
        )
      }
    },
    {
      field: "started_at",
      headerName: "started_at",
      flex: 1,
    },
    {
      field: "ended_at",
      headerName: "ended_at",
      flex: 1,
    },
    {
      field: "valid",
      headerName: "valid",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            {(row.row.valid) ? <>
              Valid
            </> :
              <>
                Invalid
              </>}
          </>
        )
      }
    },

  ];
  // Selected Chat 
  const [selectedIndex, setSelectedIndex] = React.useState(1);
  const [MessagesChat, setMessagesChat] = React.useState([]);
  const [openAdd, setOpenAdd] = React.useState(false);
  const [chatTitleView, setChatTitleView] = useState('');

  const handleCloseAdd = () => setOpenAdd(false);
  const handleListItemClick = (event, index) => {
    console.log(index)
    setSelectedIndex(index._id);
    setMessagesChat(index.messages)
    setChatTitleView(index.title_room)
    setOpenAdd(true);


  };
  return (
    <>
      <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/">
            <HomeIcon />
          </Link>
          <Link underline="hover" color="inherit" href="/users">
            {/* <HomeIcon /> */}
            Users
          </Link>


          <Typography color="text.primary">{name === undefined ? <span>NULL</span> : name}</Typography>
        </Breadcrumbs>

      </Box>
      <div style={{ height: '550px', overflowY: 'scroll' }}>
        {/* <Box m="20px" > */}
        <Grid container spacing={2}>

          <Grid item xs={12} md={12}>
            <Box
              sx={{
                backgroundColor: `${colors.blueAccent[900]}`,
                border: '1px solid #f1eded',
                marginBottom: '14px',
                padding: '10px',
                borderRadius: '6px',
              }}
            >
              <Grid container spacing={2}>
                <Grid item xs={12} md={4}>
                  <Box
                    sx={{
                      backgroundColor: `${colors.primary[400]}`,
                      border: '2px solid #dee2e6',
                      marginBottom: '14px',
                      padding: '20px',
                      borderRadius: '6px',

                    }}
                  >
                    <Grid container spacing={2}>
                      <Grid item xs={12} md={4}>
                        <Avatar style={{ height: '80px', width: '80px', backgroundColor: `${colors.blueAccent[800]}`, color: `${colors.grey[100]}`, borderRadius: '50%' }}>
                          <PersonIcon fontSize='large' />
                        </Avatar>

                      </Grid>
                      <Grid item xs={12} md={8}>
                        <Typography
                          variant="h3"
                          color="rgb(62 67 76)"
                          fontWeight="bolder"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '17px'
                          }}
                        >
                          Profile Info

                        </Typography>

                        <Typography
                          color="rgb(123, 128, 154)"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '15px'
                          }}
                        >
                          {name === undefined ? <span>NULL</span> : name}

                        </Typography>
                        <Typography
                          color="rgb(123, 128, 154)"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '15px'
                          }}
                        >
                          {type_of_customer === undefined ? <span>NULL</span> : type_of_customer}

                        </Typography>
                      </Grid>



                    </Grid>
                  </Box>
                  <Box
                    sx={{
                      backgroundColor: `${colors.primary[400]}`,
                      border: '2px solid #dee2e6',
                      marginBottom: '14px',
                      padding: '20px',
                      borderRadius: '6px',

                    }}
                  >
                    <Grid container spacing={2}>

                      <Grid item xs={12} md={12}>
                        <Typography
                          variant="h3"
                          color="rgb(62 67 76)"
                          fontWeight="bolder"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '17px'
                          }}
                        >
                          User Chat Rooms

                        </Typography>
                        {/* List Start  */}
                        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper', height: '200px', overflowY: 'scroll' }}>
                          {dataChats.map((row) => (
                            <>
                              <ListItemButton style={{ borderBottom: '.5px solid rgb(209 209 209)', cursor: 'pointer' }}
                                // onClick={()=>console.log(row)}
                                selected={selectedIndex === row._id}
                                onClick={(event) => handleListItemClick(event, row)}
                              >
                                <ListItemText primary={row.title_room === undefined ? <>Null</> : <>{row.title_room}</>}
                                  secondary={row.messages === undefined ? <>Null</> : <>Total Messages:{row.messages.length}</>} />
                              </ListItemButton>
                              {/* <ListItem alignItems="flex-start" style={{borderBottom:'.5px solid rgb(209 209 209)',cursor:'pointer'}}>
                              <ListItemText
                                primary={row.title_room===undefined?<>Null</>:<>{row.title_room}</>}
                                secondary={row.messages===undefined?<>Null</>:<>Total Messages:{row.messages.length}</>}
                              />
                            </ListItem> */}
                            </>
                          ))}
                        
                        </List>
                        {/* List End  */}
                      </Grid>
                    </Grid>
                  </Box>
                </Grid>

                <Grid item xs={12} md={8}>
                  <Box
                    sx={{
                      backgroundColor: `${colors.primary[400]}`,
                      border: '2px solid #f1eded',
                      marginBottom: '14px',
                      padding: '20px',
                      borderRadius: '6px',

                    }}
                  >
                    <Grid container spacing={2}>
                      <Grid item xs={12} md={10}>
                        <Typography
                          color="rgb(62 67 76)"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '17px',
                            fontWeight: 700
                          }}
                        >
                          Subscription History

                        </Typography>
                      </Grid>
                      <Grid item xs={12} md={12}>
                        <Box
                          m="5px 0 0 0px"
                          height="300px"
                          sx={{
                            overflowY: 'scroll',
                            "& .MuiDataGrid-root": {
                              border: "none",
                            },
                            "& .MuiDataGrid-cell": {
                              borderBottom: "none",
                            },
                            "& .name-column--cell": {
                              color: colors.greenAccent[300],
                            },
                            "& .MuiDataGrid-columnHeaders": {
                              backgroundColor: colors.grey[400],
                              borderBottom: "none",
                            },
                            "& .MuiDataGrid-virtualScroller": {
                              backgroundColor: colors.primary[400],
                            },
                            "& .MuiDataGrid-footerContainer": {
                              borderTop: "none",
                              backgroundColor: colors.grey[400],
                            },
                            "& .MuiCheckbox-root": {
                              color: `${colors.greenAccent[200]} !important`,
                            },
                          }}
                        >
                          <DataGrid
                            // {...data}

                            rows={dataOrders}
                            columns={columns}
                            getRowId={(row) => row._id}
                            loading={loading}

                          />
                        </Box>

                      </Grid>
                      {/* <Grid item xs={12} md={2}>
                        <Typography
                          color="rgb(123, 128, 154)"
                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '17px',
                            fontWeight: 500
                          }}
                        >
                          {customer_filer_status}

                        </Typography>
                      </Grid>


                      <Grid item xs={12} md={12} style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between" }}>
                        <Typography
                          sx={{
                            fontSize: '15px',
                            color: `${colors.grey[200]}`

                          }}
                        >
                          Account No

                        </Typography>
                        <Typography
                          sx={{
                            fontSize: '17px',
                            fontWeight: 500,
                            color: `${colors.grey[200]}`
                          }}
                        >
                          {account_number}
                        </Typography>
                      </Grid>



                      <Grid item xs={12} md={12} style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between" }}>
                        <Typography
                          sx={{
                            fontSize: '15px',
                            color: `${colors.grey[200]}`

                          }}
                        >
                          Sales Tax Number

                        </Typography>
                        <Typography

                          sx={{
                            fontSize: '17px',
                            fontWeight: 500,
                            color: `${colors.grey[200]}`

                          }}
                        >
                          {sales_tax_number}
                        </Typography>
                      </Grid>


                      <Grid item xs={12} md={12} style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between" }}>
                        <Typography
                          sx={{
                            fontSize: '15px',
                            color: `${colors.grey[200]}`

                          }}
                        >
                          Tax Payable

                        </Typography>
                        <Typography

                          sx={{
                            m: "0 0 5px 0",
                            fontSize: '17px',
                            fontWeight: 500,
                            color: `${colors.grey[200]}`

                          }}
                        >
                          {tax_payable}
                        </Typography>
                      </Grid> */}

                    </Grid>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Grid>
        </Grid>
        <div>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={openAdd}
            onClose={handleCloseAdd}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={openAdd}>
              <Box sx={style}>
                <Grid container spacing={2} >
                  <Grid item xs={12} md={12} mt={3}>
                    <Typography variant="h4" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                      {chatTitleView}
                    </Typography>

                  </Grid>

                  <Grid item xs={12} md={12} mt style={{ height: '400px', overflowY: 'scroll' }}>
                    <Grid container spacing={2} >
                      {MessagesChat.map((row) => (
                        <>
                          {row.sent_by === 'system' ? <>
                            <Grid item xs={12} md={12} mt={1} align="right" >
                              <div
                                style={{ backgroundColor: `${colors.grey[900]}`, width: '100%', border: '1px solid black', padding: '5px', borderRadius: '10px' }}>
                                  <Typography color={colors.grey[700]} style={{fontSize:'12px'}}  gutterBottom>
                                    Chat-GPT
                                </Typography>
                                <Typography color={colors.grey[300]} variant="h6" gutterBottom>
                                {row.message}
                                </Typography>
                                <Typography color={colors.grey[700]} style={{fontSize:'10px'}} >
                                   {row.status==='liked'?<><FavoriteIcon/></>:<></>}
                                   {row.status==='unliked'?<><HeartBrokenIcon/></>:<></>}
                                   {/* {row.status==='none'?<></>:<></>} */}


                                </Typography>
                               </div>
                            </Grid>
                          </>
                            :
                            <>
                            <Grid item xs={12} md={12} mt={1} align="left" >
                            <div
                              style={{ backgroundColor: `${colors.grey[700]}`, width: '100%', border: '1px solid black', padding: '5px', borderRadius: '10px' }}>
                              <Typography color={colors.grey[200]} style={{fontSize:'12px'}}  gutterBottom>
                                    {name}
                                </Typography>
                                <Typography color={colors.grey[100]} variant="h6"  gutterBottom>
                                {row.message}
                                </Typography>
                              </div>
                          </Grid>
                          </>
                          }
                        </>
                      ))}

                    </Grid>


                  </Grid>
                </Grid>
              </Box>
            </Fade>
          </Modal>
        </div>
        {/* </Box> */}
      </div>
    </>
  );
};

export default CustomerView;
