import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Grid, useTheme } from '@mui/material'
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import image from '../../components/Images/logo.png'
import DataBg from '../../components/Images/illution.png'
import useWindowSize from "./useWindowSize";
import { Typography } from "@mui/material";
import { tokens } from "../../theme";

import Avatar from '@mui/material/Avatar';
import url from '../url'
import ClipLoader from "react-spinners/ClipLoader";
import TextField from '@mui/material/TextField';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const InputStyle = {
  width: '100%',
}

const headingStyle = {
  fontSize: '16px',
  color: 'black'

}
const override = {
  display: ' block',
  margin: '0 auto',
}
const logoStyle = {
  width: '40%',
  height: '50%',
  marginBottom: '10px'
}

const color = "black"

function ForgetPassword() {
  const theme = useTheme();

  const colors = tokens(theme.palette.mode);
  const windowSize = useWindowSize();
  const [loading, setLoading] = useState("");
  const [loading1, setLoading1] = useState(false);
  const ContainerStyle = {
    padding: '20px',
    paddingTop: '80px',
    height: '110vh',
    backgroundColor: `${colors.grey[124]}`

  }
  const btn = {
    width: '99%',
    color: `${colors.grey[123]}`,
    backgroundColor: `${colors.grey[111]}`,
    borderColor: '#ada6f2',
    height: '50px',
    padding: '0px',
  }
  // SnakBar 
  const [snackbarType, setsnackbarType] = useState('')
  const [snackbarMsg, setsnackbarMsg] = useState('')

  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)

    }, 3000)
  }, [])
  //    Get 
  let navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  //  const [idData, setidData] = useState("");

  const headers = {
    'Content-Type': 'application/json'
  }

  const [PasswordField, setPasswordField] = useState(false)
  const [otpCode, setOtpCode] = useState('')
  const [newpassword, setNewPassword] = useState('')
  const [IdUser, setIdUser] = useState('')

  const [ButtonShowChLogin, setButtonShowChLogin] = useState(true)
  const [ButtonShowChUpdate, setButtonShowChUpdate] = useState(false)

  const [ButtonShowChVerify, setButtonShowChVerify] = useState(false)
  const [newPasswordField, setnewPasswordField] = useState(false)
  const [emailDisabled,setemailDisabled]=useState(false)
  const submitHandlerOtpVerify = async (e) => {
    if (password === '') {
      setLoading1(true)
      console.log('Empty Field')
      setsnackbarType("error")
      setsnackbarMsg("Please Enter OTP to continue")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading1(false)
      }, 3000)
      // Swal.fire('Please Enter OTP')
    } else {
      // setLoading1(true)
      console.log(otpCode)
      console.log(password)

      if (parseInt(password) === parseInt(otpCode)) {
        console.log(' matched')

        setLoading1(true)
        setsnackbarType("success")
        setsnackbarMsg("OTP matched Successfully. Enter New Password")
        setOpen(true);
        setTimeout(() => {
          setPasswordField(false)
          setnewPasswordField(true)
          setButtonShowChLogin(false)
          setButtonShowChVerify(false)
          setButtonShowChUpdate(true)
          axios.get(`${url}api/admin/get-admin-by-ID/${IdUserData}`
          , { headers }).then(response => {
            console.log(response.data)
            setIdUser(response.data.data[0]._id)

          })
            .catch(err => {
              console.log(err)
              setsnackbarType("error")
              setsnackbarMsg("Something Went Wrong")
              setOpen(true);
            })
          setOpen(false);
          setLoading1(false)
        }, 3000)
      }else{
        console.log('not matched')
        setLoading1(true)
        console.log('Not Matched')
        setsnackbarType("error")
        setsnackbarMsg("OTP Doesnot matched")
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
          setLoading1(false)
        }, 3000)
      }
    }


  }
  const submitHandlerUpdatePass = async (e) => {
    if (newpassword === '') {
      setLoading1(true)
      console.log('Empty Field')
      setsnackbarType("error")
      setsnackbarMsg("Please Enter new Password")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading1(false)
      }, 3000)
    } else {
      // Start 
      setLoading1(true)

      await axios.put(`${url}api/admin/update-credentials`, {
        email: email,
        password: newpassword,
        _id: IdUser
      }, { headers }).then(response => {
        console.log(response.data)

        if (response.data.message === "Updated Successfully") {
          // console.log(response.data.user._id)
          // localStorage.setItem('items', JSON.stringify(response.data.user._id));
          // navigate('/home')
          setsnackbarType("success")
          setsnackbarMsg("Updated Successfully")
          setOpen(true);
          setTimeout(() => {
            setOpen(false);
            setLoading1(false)
            navigate('/')

          }, 3000)

        } else {
          setsnackbarType("error")
          setsnackbarMsg("Something went Wrong")
          setOpen(true);
          setTimeout(() => {
            setOpen(false);
            setLoading1(false)
          }, 3000)
        }
      })
        .catch(err => {
          console.log(err)
          setsnackbarType("error")
          setsnackbarMsg("Something went Wrong")
          setOpen(true);
          setTimeout(() => {
            setOpen(false);
            setLoading1(false)
          }, 3000)
        })
      // end 
    
    }


  }
  const [IdUserData,setIdUserData]=useState('')
  const submitHandler = async (e) => {
    e.preventDefault()
    if (email === '') {
      setLoading1(true)
      console.log('Empty Field')
      setsnackbarType("error")
      setsnackbarMsg("Please Enter Email to continue")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading1(false)
      }, 3000)

    } else {
      // Loader 
      setLoading1(true)
      setTimeout(async () => {
        await axios.post(`${url}api/admin/otpEmail`, {
          email: email,
        }, { headers }).then(response => {
          console.log(response.data)

          if (response.data.message === "Please check Your Email Id") {
            setOtpCode(response.data.otp)
            setsnackbarType("success")
            setsnackbarMsg("OTP Send Successfully")
            setIdUserData(response.data.data._id)
            setOpen(true);
            setTimeout(() => {
              setOpen(false);
              setemailDisabled(true)
              setButtonShowChVerify(true)
              setPasswordField(true)
              setButtonShowChLogin(false)
              setLoading1(false)
            }, 3000)

          } else if (response.data.message === "Email Id not Exist") {
            setLoading1(true)
            console.log('Empty Field')
            setsnackbarType("error")
            setsnackbarMsg("Email  Doesnot Exist")
            setOpen(true);
            setTimeout(() => {
              setOpen(false);
              setLoading1(false)
            }, 3000)
          } else {
            Swal.fire('Something went Wrong')

          }


        })
          .catch(err => {
            console.log(err)
            Swal.fire('Invalid Credentials')
          })
      }, 3000)
      //  POst Request 
      // navigate('/home')


    }

  }


  return (
    <div>
      < Grid container spacing={2} style={ContainerStyle}>
        <Grid item xs={12} md={4} >
        </Grid>
        <Grid item xs={12} md={4} >
          <Card sx={{ minWidth: 275 }}>
            < Grid container spacing={2} >
              <Grid item xs={12} md={12} style={{ padding: '30px' }}>
                <Grid align='center' style={{ margin: '20px 10px 0px 10px' }}>
                  <Avatar src={image} variant="square" style={logoStyle} ></Avatar>
                  <Typography variant="h4" style={{ color: `${colors.grey[111]}`, fontWeight: 700 }} gutterBottom>
                    Forget Password
                  </Typography>
                  <TextField style={InputStyle} id="outlined-basic" disabled={emailDisabled}
                    value={email}
                    onChange={
                      (e) => setEmail(e.target.value)
                    }
                    label="Email" variant="outlined" required />
                  <br />
                  {PasswordField ?
                    <>
                    <br/>
                      <TextField style={InputStyle} id="outlined-basic"
                        value={password}
                        onChange={
                          (e) => setPassword(e.target.value)
                        }
                        label="Enter OTP" variant="outlined" required />
                        {/* <Typography variant="h6" style={{ color: '#52ad4a',fontStyle:'underlined'}} gutterBottom>
                        Resend OTP
                    </Typography> */}
                      <Typography align='right' variant="h6" style={{ color: 'blue', textDecoration: 'underline', cursor: 'pointer' }} onClick={() => {
                        // navigate('/forget-password')
                        window.location.reload();
                      }} component="h2">
                        Resend OTP
                      </Typography>
                        

                      <br /></> : <></>
                  }
                  {newPasswordField ?
                    <>
                      <br />
                      <TextField style={InputStyle} id="outlined-basic"
                        value={newpassword}
                        onChange={
                          (e) => setNewPassword(e.target.value)
                        }
                        label="Password" variant="outlined" required />

                      <br /><br />
                    </> :
                    <>
                    </>}

                  {ButtonShowChVerify ?
                    <>
                      <Button variant="contained" onClick={
                        submitHandlerOtpVerify

                      } style={btn} >
                        {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={10} /> : <h3>Verify </h3>}
                      </Button></>
                    :
                    <>

                    </>
                  }
                  {ButtonShowChLogin ?
                    <>
                    <br/>
                      <Button variant="contained"  onClick={
                        submitHandler

                      } style={btn} >
                        {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={10} /> : <h3>Send Otp </h3>}
                      </Button></> :
                    <></>}
                  {ButtonShowChUpdate ?
                    <>
                      <Button variant="contained" onClick={
                        submitHandlerUpdatePass

                      } style={btn} >
                        {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={10} /> : <h3>Update Password </h3>}
                      </Button></> :
                    <></>}



                </Grid>
              </Grid>

            </Grid>

          </Card>
        </Grid>
        {/* SnackBar  */}
        <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert onClose={handleClose} severity={snackbarType} sx={{ width: '100%' }}>
            {snackbarMsg}
          </Alert>
        </Snackbar>



      </Grid>
    </div>
  )
}

export default ForgetPassword
