import { Box, Typography, useTheme, IconButton } from "@mui/material";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import Button from '@mui/material/Button';
import Fade from '@mui/material/Fade';
import axios from 'axios'
import Grid from "@mui/material/Grid";
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import url from '../url'
import React, { useState, useEffect, useRef } from "react";
import { Editor } from '@tinymce/tinymce-react';
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const Team = () => {
  const editorRef = useRef(null);
  const log = () => {
    console.log(editorRef.current)
    if (editorRef.current) {
      console.log(editorRef.current.getContent());
    }
  };
  const [html, setHtml] = useState('');
  const [UserId, setUserId] = useState('');

  const onChange = (e) => {
    console.log(e)
    setHtml(e);
  }
  // end 
 // SnakBar 
 const [open, setOpen] = React.useState(false);

 const handleClose = (event, reason) => {
   if (reason === 'clickaway') {
     return;
   }
   setOpen(false);
 };
 const [snackbarType, setsnackbarType] = useState('')
 const [snackbarMsg, setsnackbarMsg] = useState('')
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const savePrivacyPolicy = () => {
    console.log(UserId)
    axios.put(`${url}api/Privacy_Policy/update`, {
      _id: UserId,
      PrivacyPolicy: html,

    })
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        getAllData()
        setsnackbarType("success")
        setsnackbarMsg("Updated Successfully !")
        setOpen(true);
      })
      .catch(error => console.error(`Error:${error}`));

  }
  const getAllData = () => {
    // const items = JSON.parse(localStorage.getItem('items'));
    axios.get(`${url}api/Privacy_Policy/get-all`)
      .then((response) => {
        const allData = response.data.result.PrivacyPolicy;
        console.log(allData);
        setUserId(response.data.result._id)
        console.log("response.data.result._id")
        console.log(response.data.result._id)
        var Str = response.data.result.PrivacyPolicy
        let newStr = Str.split('&lt;').join("<")
        console.log("newStr")
        console.log(newStr)
        setHtml(newStr);
      })
      .catch(error => console.error(`Error:${error}`));

  }

  useEffect(() => {
    getAllData();

  }, []);

  return (
    <>
      <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/">
            <HomeIcon />
          </Link>

          <Typography color="text.primary">Privacy Policy</Typography>
        </Breadcrumbs>

      </Box>
      <div style={{ height: '80vh', overflowY: 'scroll' }} mb>

        <Box m="20px">
          <Header title="Privacy Policy" subtitle="Managing the Policy" />
          <div>
            <Editor
              onEditorChange={(e)=>setHtml(e)}
              // onChange={(e)=>setHtml(e)}
              // onInit={(evt, editor) => {
              //   editorRef.current = editor
              // }}
              initialValue={html}
              init={{
                height: 500,
                menubar: false,
                plugins: [
                  'advlist autolink lists link image charmap print preview anchor',
                  'searchreplace visualblocks code fullscreen',
                  'insertdatetime media table paste code help wordcount'
                ],
                toolbar: 'undo redo | formatselect | ' +
                  'bold italic backcolor | alignleft aligncenter ' +
                  'alignright alignjustify | bullist numlist outdent indent | ' +
                  'removeformat | help',
                content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
              }}
              //  value={html} onChange={onChange}
              apiKey='qxhendwylskzh47hup0n8l73co1es3qd3ju5ra3gco7ch9qk' />

            < Grid container spacing={2} >
              <Grid item xs={12} md={12} >
                <Button variant="outlined" onClick={() => savePrivacyPolicy()}>
                  Save
                </Button>
              </Grid>
            </Grid>
          </div>
          {/* snackbar bar */}
          <div>
             {/* SnackBar  */}
        <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert onClose={handleClose} severity={snackbarType}
            sx={{
              width: '100%'
            }}>
            {snackbarMsg}
          </Alert>
        </Snackbar>
          </div>
          {/* </Box> */}
        </Box>
      </div>
    </>
  );
};

export default Team;
