import { Box, Typography, useTheme, IconButton } from "@mui/material";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import Button from '@mui/material/Button';
import Fade from '@mui/material/Fade';
import Backdrop from '@mui/material/Backdrop';
import Grid from "@mui/material/Grid";
import TextField from '@mui/material/TextField';
import DeleteIcon from '@mui/icons-material/Delete';
import Modal from '@mui/material/Modal';
import Tooltip from '@mui/material/Tooltip';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
import axios from 'axios'
import Swal from 'sweetalert2'
import { useNavigate } from 'react-router-dom';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import EditIcon from '@mui/icons-material/Edit';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import ClipLoader from "react-spinners/ClipLoader";
import { useFormik } from 'formik';
import * as yup from 'yup';
import url from "../url"
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import {
  DataGrid,
  GridToolbarContainer,
  GridToolbarColumnsButton,
  GridToolbarFilterButton,
  GridToolbarExport,
  GridToolbarDensitySelector,
} from '@mui/x-data-grid';
import React, { useState, useEffect } from "react";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const color = "black"
const override = {
  display: ' block',
  margin: '0 auto',
}
const style = {
  position: 'absolute',
  overflowY:'scroll',
  height:'550px',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  // width: '100%',
  bgcolor: 'beige',
  borderRadius: '10px',
  padding: "0px 40px 40px 40px",
  boxShadow: 24,
  // p: 4,
};
const style1 = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'beige',
  borderRadius: '10px',
  boxShadow: 24,
  p: 4,
};

const Team = () => {
  const [loading1, setLoading1] = useState(false);
  const [loadingAdd, setLoadingAdd] = useState(false);
  const [hideshowarray, setHideShowArray] = useState(false)
  // SnakBar 
  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  const [snackbarType, setsnackbarType] = useState('')
  const [snackbarMsg, setsnackbarMsg] = useState('')
  // Tabs value
  const navigate = useNavigate();

  const headers = {
    'Content-Type': 'application/json'
  }
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [TitleEdit, setTitleEdit] = useState('');
  const [FeaturesEdit, setFeaturesEdit] = useState('');
  const [PriceEdit, setPriceEdit] = useState(0);
  const [NoOfSearchesEdit, setNoOfSearchesEdit] = useState('');
  const [ValidityEdit, setValidityEdit] = useState('');
  // Approve 
  const [openAdd, setOpenAdd] = React.useState(false);
  const handleOpenAdd = () => {
    setOpenAdd(true);
  }
  const handleCloseAdd = () => setOpenAdd(false);
  // Update 
  const [openUpdate, setOpenUpdate] = React.useState(false);
  const [idData, setIdData] = React.useState('');

  const handleOpenUpdate = (row) => {
    console.log(row)

    axios.get(`${url}api/Subscription_plan/get/${row}`)
      .then(response => {
        console.log('response')
        console.log(response.data.result);
        setTitleEdit(response.data.result[0].title)
if(response.data.result[0].features.length===0){

}else{
  setArrayListEdit(response.data.result[0].features)
sethideshowarrayEdit(true)
}
        setNoOfSearchesEdit(response.data.result[0].num_of_searches)
        setValidityEdit(response.data.result[0].validity)
        setPriceEdit(response.data.result[0].price)
        setIdData(response.data.result[0]._id)
        setOpenUpdate(true);
        setLoading(false)
      })
      .catch(err => {
        console.log(err)
        setsnackbarType("error")
        setsnackbarMsg(err)
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
        }, 3000)
      })
    console.log(row)
  }
  const handleCloseUpdate = () => setOpenUpdate(false);
  // Delete 
  // Alert 
  const deleteData = (id) => {
    console.log('deleting User')
    console.log(id);
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: 'btn btn-success',
        cancelButton: 'btn btn-danger'
      },
      buttonsStyling: {
        backgroundColor: '#4CAF50', /* Green */
        border: 'none',
        color: 'white',
        padding: '15px 32px',
        textAlign: 'center',
        textDecoration: 'none',
        display: 'inline-block',
        fontSize: '16px'
      }
    })
    swalWithBootstrapButtons.fire({
      title: 'Are you sure?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        axios.delete(`${url}api/Subscription_plan/delete/${id}`,
          { headers })
          .then(res => {
            console.log(res);
            console.log(res.data);
            getAllData()
            setsnackbarType("success")
            setsnackbarMsg("Subscription Plan Deleted Successfully !")
            setOpen(true);
            setOpenAdd(false);
            // setOpen1(true);
          }).catch(err => {
            console.log(err)
          })
      } else if (
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire(
          'Cancelled',
          'Subcription Plan is safe :)',
          'error'
        )
      }
    })

  }
  // View 
  const handleClickOpenUpdate = () => {
    setLoading1(true)
    if (TitleEdit === ''|| PriceEdit === '' || NoOfSearchesEdit === '' || ValidityEdit === '') {
      setsnackbarType("error")
      setsnackbarMsg("Fill All Fields to Continue !")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading1(false)
      }, 3000)
    } else {
      let ArrayData = ArrayListEdit
      let nameArr = ArrayData.join(', ');
      console.log(nameArr)
      axios.put(`${url}api/Subscription_plan/update`, {
        _id: idData,
        title: TitleEdit,
        features: nameArr,
        price: PriceEdit,
        num_of_searches: NoOfSearchesEdit,
        validity: ValidityEdit
      }, { headers }).then(response => {
        console.log(response)
        setOpenUpdate(false);
        getAllData()
        setsnackbarType("success")
        setsnackbarMsg("Subscription Plan Updated Successfully !")
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
          setLoading1(false)
        }, 3000)

      })
        .catch(err => {
          console.log(err)
        })
    }

  };
  const [tempFeature, setTempFeature] = useState();

  const [ArrayList, setArrayList] = useState([]);
  const RemoveItem = (arrayData) => {
    setArrayList(oldValues => {
      return oldValues.filter(fruit => fruit !== arrayData)
    })
    console.log(ArrayList)


  }
  const RemoveItemEdit = (arrayData) => {
    setArrayListEdit(oldValues => {
      return oldValues.filter(fruit => fruit !== arrayData)
    })
    console.log(ArrayListEdit)


  }
  const AddFeatures = () => {
    console.log(tempFeature);
    if (tempFeature === '' || tempFeature === null || tempFeature === undefined) {
      console.log('Empty')
    } else {
      console.log('Not Empty')

      if (ArrayList.length === 0) {
        setArrayList([tempFeature])
        setTempFeature('')
        setHideShowArray(true)
      } else {
        setArrayList([...ArrayList, tempFeature]);
        setTempFeature('')

      }
      console.log(ArrayList)
    }

  }
  const [ArrayListEdit, setArrayListEdit] = useState([]);
  const [hideshowarrayEdit,sethideshowarrayEdit]=useState(false)
  const AddFeaturesEdit = () => {
    console.log(FeaturesEdit);
    if (FeaturesEdit === '' || FeaturesEdit === null || FeaturesEdit === undefined) {
      console.log('Empty')
    } else {
      console.log('Not Empty')

      if (ArrayListEdit.length === 0) {
        setArrayListEdit([FeaturesEdit])
        setFeaturesEdit('')
        sethideshowarrayEdit(true)
      } else {
        setArrayListEdit([...ArrayListEdit, FeaturesEdit]);
        setFeaturesEdit('')

      }
      console.log(ArrayListEdit)
    }
  }
  // Formik 
  const validationSchema = yup.object({
    title: yup
      .string()
      .required('Required title'),
    Price: yup
      .string('Enter your Price')
      .required('Price is required')
      .matches(
        /(?=.*?\d)^\$?(([1-9]\d{0,2}(,\d{3})*)|\d+)?(\.\d{1,2})?$/,
        "only numbers "
      ),
    NoOfSearches: yup
      .string('Enter your NoOfSearches')
      .required('No Of Searches is required')
      .matches(
        /(?=.*?\d)^\$?(([1-9]\d{0,2}(,\d{3})*)|\d+)?(\.\d{1,2})?$/,
        "only numbers "
      ),
    validity: yup
      .string('Enter your validity')
      .required('validity is required'),

  });
  const formik = useFormik({
    initialValues: {
      title: '',
      Price: '',
      NoOfSearches: '',
      validity: '',

    },
    validationSchema: validationSchema,
    onSubmit: (values, { resetForm }) => {
      console.log(values)
      console.log(ArrayList)
      let ArrayData = ArrayList
      let nameArr = ArrayData.join(', ');
      console.log(nameArr)
      setLoadingAdd(true)
      setTimeout(() => {
        axios.post(`${url}api/Subscription_plan/create`, {
          title: values.title,
          features: nameArr,
          price: values.Price,
          num_of_searches: values.NoOfSearches,
          validity: values.validity
        }, { headers }).then(response => {
          console.log(response)
          // setData([...data, response.data]);
          getAllData()
          setsnackbarType("success")
          setsnackbarMsg("Subscription Plan Created Successfully !")
          setOpen(true);
          resetForm('')
          setArrayList([])
          setLoadingAdd(false)
          setOpenAdd(false);

        })
          .catch(err => {
            console.log(err)
            setsnackbarType("error")
            setsnackbarMsg(err)
            setOpen(true);
            setTimeout(() => {
              setOpen(false);
              setLoadingAdd(false)
            }, 3000)
          })
      }, 3000)

    },
  });
  const columns = [
    {
      field: "title",
      headerName: "Title",
      flex: 1,
    },
    {
      field: "price",
      headerName: "Price",
      flex: 1,
    },
    {
      field: "num_of_searches",
      headerName: "No of Searches",
      flex: 1,
    },
    {
      field: "validity",
      headerName: "Validity",
      flex: 1,
    },
    {
      field: "features",
      headerName: "features",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            {(row.row.features.length === 0) || (row.row.features === undefined) || (row.row.features === null) ? <>
              No Feature Added
            </> :
              <>
                {row.row.features[0]}...
              </>}
          </>
        )
      }
    },

    {
      field: "_id",
      headerName: "Action",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            <IconButton onClick={() => handleOpenUpdate(row.row._id)}>
              <Tooltip title="Edit">
                <EditIcon />
              </Tooltip>
            </IconButton>
            <IconButton onClick={() => deleteData(row.row._id)}>
              <Tooltip title="Delete">

                <DeleteIcon style={{ color: 'red' }} />
              </Tooltip>
            </IconButton>
          </>

        );
      },
    },
  ];
  function CustomToolbar() {
    return (
      <GridToolbarContainer style={{ marginBottom: '5px', backgroundColor: `${colors.primary[600]}` }}>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton />
        <GridToolbarDensitySelector />
        <GridToolbarExport />
      </GridToolbarContainer>
    );
  }
  //Get API Axios
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [changeView, setChangeView] = useState(true);

  const getAllData = () => {
    axios.get(`${url}api/Subscription_plan/get-all`)
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        setData(response.data.result);
        // setimagesdata(response.data.images);
        setLoading(false)
      })
      .catch(error => console.error(`Error:${error}`));

  }

  useEffect(() => {
    getAllData();

  }, []);
  const handleOpenChangeView = () => {
    if (changeView) {
      setChangeView(false)

    } else {
      setChangeView(true)

    }
  }

  return (
    <>
      <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/home">
            <HomeIcon />
          </Link>

          <Typography color="text.primary">Subscription Plan</Typography>
        </Breadcrumbs>

        <Button variant="contained"
          style={{ backgroundColor: `${colors.grey[400]}` }}
          // color={colors.grey[400]}
          onClick={() => handleOpenAdd()}>
          Add
        </Button>
      </Box>
    <div style={{height:'80vh',overflowY:'scroll'}} mb>
      
      <Box m="20px">
        <Grid container spacing={2} >
          <Grid item xs={12} md={10} mt>
            <Header title="Subscription Plan" subtitle="Managing the Plans" />

          </Grid>
          <Grid item xs={12} md={2} mt>
            <Button variant="contained"
              style={{ backgroundColor: `${colors.grey[400]}` }}
              // color={colors.grey[400]}
              onClick={() => handleOpenChangeView()}>
              Change View
            </Button>

          </Grid>

        </Grid>

        <Box sx={{ width: '100%' }}>

          <Box
            m="5px 0 0 0px"
            height="75vh"
            sx={{
              "& .MuiDataGrid-root": {
                border: "none",
              },
              "& .MuiDataGrid-cell": {
                borderBottom: "none",
              },
              "& .name-column--cell": {
                color: colors.greenAccent[300],
              },
              "& .MuiDataGrid-columnHeaders": {
                backgroundColor: colors.grey[400],
                borderBottom: "none",
              },
              "& .MuiDataGrid-virtualScroller": {
                backgroundColor: colors.primary[400],
              },
              "& .MuiDataGrid-footerContainer": {
                borderTop: "none",
                backgroundColor: colors.grey[400],
              },
              "& .MuiCheckbox-root": {
                color: `${colors.greenAccent[200]} !important`,
              },
            }}
          >
            {changeView ? <>
              <DataGrid
                // {...data}

                rows={data}
                columns={columns}
                getRowId={(row) => row._id}
                loading={loading}
                components={{
                  Toolbar: CustomToolbar,
                }}
              />
            </> :
              <>
                {/* data */}
                <Grid container spacing={2} >
                  {data.map((recipie) => (
                    <>
                      <Grid item xs={12} md={4} mt>
                        <Card sx={{ maxWidth: 345,borderRadius:'10px',padding:'10px' }}>
                          {/* <CardHeader
                            title={recipie.title === undefined ? <>Null</> : <>{recipie.title}</>}
                            style={{ borderBottom: `1px solid ${colors.grey[700]}` }}
                          /> */}
                          <CardContent>
                          <Typography variant="h5" fontWeight={700} color="text.primary">
                          {recipie.title === undefined ? <>Null</> : <>{recipie.title}</>}
                            </Typography>
                            <Typography variant="h4" fontWeight={700} color="text.secondary">
                            {recipie.price === undefined ? <>Null</> : <>{recipie.price}</>} $
                            </Typography>
                            <Grid container spacing={2} mt>
                          {recipie.features.map((arrayData) => (
                            <>
                              <Grid item xs={1} md={1}  >
                                <FiberManualRecordIcon style={{fontSize:'12px'}}/>

                              </Grid>
                              <Grid item xs={11} md={11} >
                                <Typography color={colors.grey[300]} variant="h6" gutterBottom>
                                  {arrayData}
                                </Typography>
                              </Grid>
                            </>
                          ))}

                        </Grid>
                            {/* <Typography variant="h6" color="text.primary" fontWeight={700} mt={2}>
                              {recipie.features === undefined ? <>Null</> : <>{recipie.features[0]}</>}...
                            </Typography> */}
                           <Typography variant="h6" color="text.primary" mt>
                              No of searches: {recipie.num_of_searches === undefined ? <>Null</> : <>{recipie.num_of_searches}</>}
                            </Typography>
                            <Typography variant="h6" color="text.primary">
                              Validity: {recipie.validity === undefined ? <>Null</> : <>{recipie.validity}</>}
                            </Typography>
                          </CardContent>
                          <CardActions disableSpacing>
                            <IconButton aria-label="View" onClick={() => handleOpenUpdate(recipie._id)}>
                              <EditIcon />
                            </IconButton>
                            <IconButton aria-label="Delete" onClick={() => deleteData(recipie._id)}>
                              <DeleteIcon style={{ color: 'red' }} />
                            </IconButton>

                          </CardActions>
                        </Card>
                      </Grid>

                    </>
                  ))}
                </Grid>


              </>}
          </Box>
          {/* </TabPanel> */}

        </Box>
        {/* SnackBar  */}
        <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert onClose={handleClose} severity={snackbarType}
            sx={{
              width: '100%'
            }}>
            {snackbarMsg}
          </Alert>
        </Snackbar>

        {/* Add  */}
        <div>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={openAdd}
            onClose={handleCloseAdd}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={openAdd}>
              <Box sx={style}>
                <form onSubmit={formik.handleSubmit} >
                  <Grid container spacing={2} >
                    <Grid item xs={12} md={12} mt={3}>
                      <Typography variant="h3" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                        Add Subscription Plan
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={3}>
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        Title
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={9}>

                      <TextField
                        value={formik.values.title}
                        onChange={(e) => formik.setFieldValue("title", e.target.value)}
                        error={formik.touched.title && Boolean(formik.errors.title)}
                        helperText={formik.touched.title && formik.errors.title}
                        // value={title}
                        style={{ width: '100%' }}
                        // onChange={(e) => 
                        //   setTitle(e.target.value)
                        // } 
                        id="filled-basic"
                        variant="filled" />
                    </Grid>
                    <Grid item xs={12} md={3}>
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        Features
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={7}>
                      <TextField
                        value={tempFeature}
                        style={{ width: '100%' }}
                        onChange={(e) => setTempFeature(e.target.value)
                        }
                        id="filled-basic"
                        variant="filled" />

                    </Grid>
                    <Grid item xs={12} md={2}>

                      <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}`, width: '60px', height: '50px' }}
                        onClick={() => AddFeatures()
                        }
                      >
                        Add

                      </Button>
                    </Grid>
                    <Grid item xs={12} md={12} align="left">
                      {hideshowarray ? <>
                        <Grid container spacing={2}>
                          {ArrayList.map((arrayData) => (
                            <>
                              <Grid item xs={1} md={1} >
                                <FiberManualRecordIcon style={{fontSize:'12px'}}/>

                              </Grid>
                              <Grid item xs={10} md={10} >
                                <Typography color={colors.grey[300]} variant="h6" gutterBottom>
                                  {arrayData}
                                </Typography>
                              </Grid>
                              <Grid item xs={1} md={1} >
                                <RemoveCircleOutlineIcon onClick={() => RemoveItem(arrayData)} style={{ cursor: 'pointer' }} />
                              </Grid>
                            </>
                          ))}

                        </Grid>
                      </> : <></>}


                    </Grid>
                    <Grid item xs={12} md={3}>
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        No of Searches
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={9}>

                      <TextField
                        value={formik.values.NoOfSearches}
                        onChange={(e) => formik.setFieldValue("NoOfSearches", e.target.value)}
                        error={formik.touched.NoOfSearches && Boolean(formik.errors.NoOfSearches)}
                        helperText={formik.touched.NoOfSearches && formik.errors.NoOfSearches}
                        //  value={NoOfSearches} 
                        style={{ width: '100%' }}
                        // onChange={(e) => setNoOfSearches(e.target.value)
                        // } 
                        id="filled-basic" variant="filled" />
                    </Grid>
                    <Grid item xs={12} md={3}>
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        Validity
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={9}>

                      <TextField
                        value={formik.values.validity}
                        onChange={(e) => formik.setFieldValue("validity", e.target.value)}
                        error={formik.touched.validity && Boolean(formik.errors.validity)}
                        helperText={formik.touched.validity && formik.errors.validity}
                        // value={validity}
                        style={{ width: '100%' }}
                        id="filled-basic" variant="filled" />
                    </Grid>
                    <Grid item xs={12} md={3}>
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        Price
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={9}>
                      <TextField
                        value={formik.values.Price}
                        onChange={(e) => formik.setFieldValue("Price", e.target.value)}
                        error={formik.touched.Price && Boolean(formik.errors.Price)}
                        helperText={formik.touched.Price && formik.errors.Price}
                        //  value={Price}
                        style={{ width: '100%' }}
                        id="filled-basic" variant="filled" />
                    </Grid>
                    <Grid item xs={12} md={12} align="center">

                      <Button variant="contained" type="submit" style={{ backgroundColor: `${colors.grey[400]}`, width: '100px', height: '50px' }}
                      >
                        {loadingAdd ? <ClipLoader color={color} loading={loadingAdd} css={override} size={30} /> : <h3>Submit</h3>}

                      </Button>
                    </Grid>
                  </Grid>
                </form>
              </Box>
            </Fade>
          </Modal>
        </div>
        {/* Update  */}
        <div>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={openUpdate}
            onClose={handleCloseUpdate}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={openUpdate}>
              <Box sx={style}>

                <Grid container spacing={2} >
                  <Grid item xs={12} md={12} mt={3}>
                    <Typography variant="h3" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                      Edit Subscription Plan
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Title
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>
                    <TextField value={TitleEdit} style={{ width: '100%' }}
                      onChange={(e) => setTitleEdit(e.target.value)
                      } id="filled-basic" variant="filled" />
                  </Grid>
                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Features
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={7}>
                    {/* featutres  */}
                    {/* <form onSubmit={handleSubmit}> */}
                    <TextField value={FeaturesEdit} style={{ width: '100%' }}
                      onChange={(e) => setFeaturesEdit(e.target.value)
                      } id="filled-basic"
                      variant="filled" />

                  </Grid>
                  <Grid item xs={12} md={2}>
                    <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}`, width: '60px', height: '50px' }}
                      onClick={() => AddFeaturesEdit()
                      }
                    >
                      Add

                    </Button>
                  </Grid>
                  <Grid item xs={12} md={12} align="left" style={{padding:'20px'}}>
                      {hideshowarrayEdit ? <>
                        <Grid container spacing={2}>
                          {ArrayListEdit.map((arrayData) => (
                            <>
                              <Grid item xs={1} md={1} >
                                <FiberManualRecordIcon style={{fontSize:'12px'}}/>
                              </Grid>
                              <Grid item xs={10} md={10} >
                                <Typography color={colors.grey[300]} variant="h6" gutterBottom>
                                  {arrayData}
                                </Typography>
                              </Grid>
                              <Grid item xs={1} md={1} >
                                <RemoveCircleOutlineIcon onClick={() => RemoveItemEdit(arrayData)} style={{ cursor: 'pointer' }} />
                              </Grid>
                            </>
                          ))}

                        </Grid>
                      </> : <></>}


                    </Grid>
                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      No of Searches
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>

                    <TextField value={NoOfSearchesEdit} style={{ width: '100%' }}
                      onChange={(e) => setNoOfSearchesEdit(e.target.value)
                      } id="filled-basic" variant="filled" />
                  </Grid>
                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Validity
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>

                    <TextField value={ValidityEdit} style={{ width: '100%' }}
                      onChange={(e) => setValidityEdit(e.target.value)
                      } id="filled-basic" variant="filled" />
                  </Grid>


                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Price
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>

                    <TextField value={PriceEdit} style={{ width: '100%' }}
                      onChange={(e) => setPriceEdit(e.target.value)
                      } id="filled-basic" variant="filled" />
                  </Grid>
                  <Grid item xs={12} md={12} align="center">
                    <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}`, width: '100px', height: '50px' }} onClick={() => handleClickOpenUpdate()}>
                      {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={30} /> : <h3>Update</h3>}

                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Fade>
          </Modal>
        </div>
      </Box>
      </div>
    </>
  );
};

export default Team;
