import React, { useState, useEffect } from "react";
import { Box, Typography, useTheme } from "@mui/material";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import StatBox from "../../components/StatBox";
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import GroupIcon from '@mui/icons-material/Group';
import url from '../url'
import EventIcon from '@mui/icons-material/Event';
import Grid from "@mui/material/Grid";
import CardMembershipIcon from '@mui/icons-material/CardMembership';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import Avatar from '@mui/material/Avatar';

import {
  DataGrid,
  GridToolbarContainer,
} from '@mui/x-data-grid';

const Dashboard = () => {
  const theme = useTheme();
  const navigate = useNavigate();

  const colors = tokens(theme.palette.mode);
  const [items, setItems] = useState([]);

  useEffect(() => {
    const items = JSON.parse(localStorage.getItem('items'));
    console.log(items)
    if (items == null) {

      navigate('/')


    } else {
      setItems(items);
      console.log("items")
      console.log(items)
      // navigate('/dashboard')
    }
  }, []);
  //Get API Axios
  const [data, setData] = useState('');
  const [loading, setLoading] = useState(true);
  const getAllData = () => {
    axios.get(`${url}api/user/get-all`)
      .then((response) => {
        const allData = response.data.length;
        console.log(allData);
        setData(response.data.result.length);
        // setimagesdata(response.data.images);

        setLoading(false)
      })
      .catch(error => console.error(`Error:${error}`));

  }
  const [data1, setData1] = useState('');
  const getAllData1 = () => {
    axios.get(`${url}api/UserSubscription/getTotalSubscriptions`)
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        setData1(response.data.result.length);
      })
      .catch(error => console.error(`Error:${error}`));

  }
  const [data2, setData2] = useState('');
  const [addedByName, setAddedByName] = useState('');
  const [profileImg, setprofileImg] = useState('');
  const [notes, setNotes] = useState([]);

  const getAllData2 = () => {
    axios.get(`${url}api/AppUpdates/get-all`)
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        setAddedByName(response.data.result[0].name)
        setprofileImg(response.data.result[0].profile_image)
        setNotes(response.data.result[0].notes)

        setData2(response.data.result[0].creation_date);
      })
      .catch(error => console.error(`Error:${error}`));

  }
  const getAllDataReport = () => {
    axios.get(`${url}api/UserSubscription/get-all`).then((response) => {
      const allData = response.data;
      console.log(allData);
      setDataReport(response.data.result);
      setLoading(false)
    })
      .catch(error => console.error(`Error:${error}`));

  }
  useEffect(() => {
    getAllData();
    getAllData1();
    getAllData2();
    getAllDataReport();

  }, []);
  function CustomToolbar() {
    return (
      <GridToolbarContainer style={{ marginBottom: '5px' }}>
         < Grid container spacing={2} >
            <Grid item xs={12} md={4} >
            <Typography variant="h4" color="text.primary" fontWeight={700}>
                            All Subscribers
                          </Typography>

            </Grid>
            </Grid>

      </GridToolbarContainer>
    );
  }
  const [dataReports, setDataReport] = useState([]);
  const columnsReports = [
    {
      field: "userId",
      headerName: "User",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            <span>{(row.row.userId === undefined)||(row.row.userId === null)||(row.row.userId === '') ? <span>NUll</span> : <span>{row.row.userId.email}</span>}</span>
          </>
        )
      }
    },
    {
      field: "subscriptionPlan",
      headerName: "Subscription Plan",
      // type: "number",
      flex: 1,
      headerAlign: "left",
      align: "left",
      renderCell: (row) => {
        return (
          <>
            <span>{(row.row.subscriptionPlan === undefined)||(row.row.subscriptionPlan === null)||(row.row.subscriptionPlan === '') ? <span>NUll</span> : <span>{row.row.subscriptionPlan.title}</span>}</span>
          </>
        )
      }
    },
    {
      field: "started_at",
      headerName: "Started At",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            <span>{(row.row.started_at === undefined)||(row.row.started_at === null)||(row.row.started_at === '') ? <span>NUll</span> : <span>{row.row.started_at}</span>}</span>
          </>
        )
      }
    },
    {
      field: "ended_at",
      headerName: "Ended At",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            <span>{(row.row.ended_at === undefined)||(row.row.ended_at === null)||(row.row.ended_at === '') ? <span>NUll</span> : <span>{row.row.ended_at}</span>}</span>
          </>
        )
      }
    },

  ];


  return (
    <>
    <div style={{height:'85vh',overflowY:'scroll'}}>
      <Box m="20px" >
        {/* HEADER */}
        <Box display="flex" justifyContent="space-between" alignItems="center">
          <Header title="DASHBOARD" subtitle="Welcome to your dashboard" />
        </Box>

        {/* GRID & CHARTS */}
        <Box
          display="grid"
          gap="20px"
        >
          {/* ROW 1 */}
          < Grid container spacing={2} >
            <Grid item xs={12} md={4} >
              <Box
                gridColumn="span 4"
                p={2}
                backgroundColor={colors.primary[400]}
                display="flex"
                alignItems="center"
                justifyContent="center"
              >
                <StatBox
                  title={data}
                  subtitle="Total Users"
                  progress="0.75"
                  increase={data}
                  icon={
                    <GroupIcon
                      sx={{ color: colors.grey[600], fontSize: "26px" }}
                    />
                  }
                />
              </Box>

            </Grid>
            <Grid item xs={12} md={4} >
              <Box
                gridColumn="span 4"
                backgroundColor={colors.primary[400]}
                display="flex"
                alignItems="center"
                p={2}
                justifyContent="center"
              >
                <StatBox
                  title={data1}
                  subtitle="Total Subscribers"
                  increase={data1}
                  icon={
                    <CardMembershipIcon
                      sx={{ color: colors.grey[600], fontSize: "26px" }}
                    />
                  }
                />
              </Box>
            </Grid>
            <Grid item xs={12} md={4} >
              <Box
                gridColumn="span 4"
                backgroundColor={colors.primary[400]}
                display="flex"
                p={2}
                alignItems="center"
                justifyContent="center"
              >
                <StatBox
                  title="Latest Release"
                  subtitle="Release Date"
                  increase={data2.slice(0,10)}
                  icon={
                    <EventIcon
                      sx={{ color: colors.grey[600], fontSize: "26px" }}
                    />
                  }
                />
              </Box>
            </Grid>
          </Grid>

        </Box>
      </Box>
      <Box m="20px">
      < Grid container spacing={2} >
            <Grid item xs={12} md={8} >
            <Box
          m="5px 0 0 0"
          height="50vh"
          sx={{
            "& .MuiDataGrid-root": {
              border: "none",
            },
            "& .MuiDataGrid-cell": {
              borderBottom: "none",
            },
            "& .name-column--cell": {
              color: colors.greenAccent[300],
            },
            "& .MuiDataGrid-columnHeaders": {
              backgroundColor: colors.grey[400],
              borderBottom: "none",
            },
            "& .MuiDataGrid-virtualScroller": {
              backgroundColor: colors.primary[400],
            },
            "& .MuiDataGrid-footerContainer": {
              borderTop: "none",
              backgroundColor: colors.grey[400],
            },
            "& .MuiCheckbox-root": {
              color: `${colors.greenAccent[200]} !important`,
            },
          }}
        >
          <DataGrid
            // {...data}

            rows={dataReports}
            columns={columnsReports}
            getRowId={(row) => row._id}
            loading={loading}
            components={{
              Toolbar: CustomToolbar,
            }}
          />
        </Box>
              </Grid>
              <Grid item xs={12} md={4} mt={5}>
             
              <Card sx={{ maxWidth: 345 }}>
              <Typography variant="h6"
                    color={colors.grey[300]} style={{ fontSize: '20px', margin: '20px', fontWeight: 'bold' }}>Latest App Release Detail</Typography>
                      <CardHeader
                        avatar={
                          <Avatar src={`${url}${profileImg}`}  />
                        }
                        title={`Added by :${addedByName}`}
                        subheader={data2.slice(0, 10)}
                      />
                      <CardContent>
                        {notes.map((row1) => (
                          <Typography variant="body2" color="text.secondary">
                            {row1.title} : {row1.description}
                          </Typography>
                        ))}

                      </CardContent>
                    </Card>
              </Grid>
              </Grid>
        {/* <Header title="Users" subtitle="Managing the Users" /> */}
        {/* <Box
          m="5px 0 0 0"
          height="75vh"
          sx={{
            "& .MuiDataGrid-root": {
              border: "none",
            },
            "& .MuiDataGrid-cell": {
              borderBottom: "none",
            },
            "& .name-column--cell": {
              color: colors.greenAccent[300],
            },
            "& .MuiDataGrid-columnHeaders": {
              backgroundColor: colors.grey[400],
              borderBottom: "none",
            },
            "& .MuiDataGrid-virtualScroller": {
              backgroundColor: colors.primary[400],
            },
            "& .MuiDataGrid-footerContainer": {
              borderTop: "none",
              backgroundColor: colors.grey[400],
            },
            "& .MuiCheckbox-root": {
              color: `${colors.greenAccent[200]} !important`,
            },
          }}
        >
          <DataGrid
            // {...data}

            rows={dataReports}
            columns={columnsReports}
            getRowId={(row) => row._id}
            loading={loading}
            components={{
              Toolbar: CustomToolbar,
            }}
          />
        </Box> */}
        {/* </Box> */}
      </Box>
      </div>
    </>
  );
};

export default Dashboard;
