import React, { useState, useEffect, useRef } from "react";
import { ProSidebar, Menu, MenuItem } from "react-pro-sidebar";
import { Box, IconButton, Typography, useTheme } from "@mui/material";
import { Link } from "react-router-dom";
import "react-pro-sidebar/dist/css/styles.css";
import { tokens } from "../../theme";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import MenuOutlinedIcon from "@mui/icons-material/MenuOutlined";
import SettingsIcon from '@mui/icons-material/Settings';
import Avatar from '@mui/material/Avatar';
import image from '../../components/Images/logo.png'
import { Grid } from '@mui/material'
import CategoryIcon from '@mui/icons-material/Category';
import DinnerDiningIcon from '@mui/icons-material/DinnerDining';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import SyncIcon from '@mui/icons-material/Sync';
import LockIcon from '@mui/icons-material/Lock';
import LineWeightIcon from '@mui/icons-material/LineWeight';
import CardMembershipIcon from '@mui/icons-material/CardMembership';
const Item = ({ title, to, icon, selected, setSelected }) => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  return (
    <MenuItem
      active={selected === title}
      style={{
        color: `${colors.grey[123]}`,
      }}
      onClick={() => setSelected(title)}
      icon={icon}
    >
      <Typography>{title}</Typography>
      <Link to={to} />
    </MenuItem>
  );
};
const logoStyle = {
  width: '100px',
  height: '50px',
}
const Sidebar = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [selected, setSelected] = useState("Dashboard");
  const [btnHideSmall,setbtnHideSmall] =useState(true)
  useEffect(() => {
    const interval = setInterval(() => {
      if (window.innerWidth < 400) {
        // console.log(window.innerWidth)
        setIsCollapsed(true)
        setbtnHideSmall(false)
      }else{
        // console.log(window.innerWidth)
        setbtnHideSmall(true)
      }
    }, 1000);
    return () => clearInterval(interval);


  }, []);

  return (
    <Box
      sx={{
        "& .pro-sidebar-inner": {
          background: `${colors.primary[900]} !important`,
        },
        "& .pro-icon-wrapper": {
          backgroundColor: "transparent !important",
        },
        "& .pro-inner-item": {
          padding: "5px 35px 5px 20px !important",
        },
        "& .pro-inner-item:hover": {
          color: "#10A37F !important",
        },
        "& .pro-menu-item.active": {
          color: "#10A37F !important",
        },
      }}
    >
      <ProSidebar collapsed={isCollapsed}>
        <Menu iconShape="square">
          {/* LOGO AND MENU ICON */}
{btnHideSmall?<>
  <MenuItem
            onClick={() => setIsCollapsed(!isCollapsed)}
            icon={isCollapsed ? <MenuOutlinedIcon /> : undefined}
            style={{
              margin: "20px 0px 20px 0px",
              color: colors.grey[123],
              borderBottom:`1px solid ${colors.grey[123]}`
            }}
          >
            {!isCollapsed && (
              <Box
                display="flex"
                justifyContent="space-between"
                alignItems="center"
                ml="15px"
              >
                < Grid container spacing={2}>
                {/* <Grid item xs={12} md={12} >
                    <IconButton onClick={() => setIsCollapsed(!isCollapsed)}>
                      <MenuOutlinedIcon />
                    </IconButton>
                  </Grid> */}
                  <Grid item xs={12} md={12} align="center">
                  <Typography  variant="h3" style={{ color: colors.grey[123],fontWeight:700  }} onClick={() => {
                        // navigate('/forget-password')
                      }} component="h2">
                        Chat GPT
                      </Typography>
                    {/* <Avatar src={image} variant="square" style={logoStyle} ></Avatar> */}


                  </Grid>
                 
                </Grid>
                {/* <Typography variant="h3" color={colors.grey[100]}>
                  Recipie App
                </Typography> */}

              </Box>
            )}
          </MenuItem>
</>:<></>}
          

          <Box paddingLeft={isCollapsed ? undefined : "10%"}>
            <Item
              title="Dashboard"
              to="/home"
              icon={<HomeOutlinedIcon />}
              selected={selected}
              setSelected={setSelected}

            />
             <Item
              title="Subscription Plans"
              to="/subscriptionPlan"
              icon={<CardMembershipIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Users"
              to="/users"
              icon={<PeopleAltIcon />}
              selected={selected}
              setSelected={setSelected}
            />
             <Item
              title="Manage App Updates"
              to="/appUpdates"
              icon={<SyncIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Privacy Policy"
              to="/privacypolicy"
              icon={<LockIcon />}
              selected={selected}
              setSelected={setSelected}
            />
              <Item
              title="Terms and Conditions"
              to="/termsAndConditions"
              icon={<LineWeightIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Settings"
              to="/settings"
              icon={<SettingsIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            

            {/* <Typography
              variant="h6"
              color={colors.grey[123]}
              sx={{ m: "15px 0 5px 20px" }}
            >
              Data
            </Typography> */}
            {/* <Item
              title="Categories"
              to="/categories"
              icon={<CategoryIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Recipes"
              to="/recipies"
              icon={<DinnerDiningIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Users"
              to="/users"
              icon={<PeopleAltIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Item
              title="Settings"
              to="/settings"
              icon={<SettingsIcon />}
              selected={selected}
              setSelected={setSelected}
            /> */}

            {/* <Item
              title="Membership Requests"
              to="/membershipRequests"
              icon={<CardMembershipIcon />}
              selected={selected}
              setSelected={setSelected}
            />
           
             <Item
              title="Manage Members"
              to="/manageMembers"
              icon={<GroupsIcon />}
              selected={selected}
              setSelected={setSelected}
            />
             <Item
              title="Events"
              to="/events"
              icon={<EventIcon />}
              selected={selected}
              setSelected={setSelected}
            /> */}
            {/* 
              <Item
              title="Meetings"
              to="/meetings"
              icon={<MeetingRoomIcon />}
              selected={selected}
              setSelected={setSelected}
            />
             <Item
              title="Council"
              to="/council"
              icon={<CorporateFareIcon />}
              selected={selected}
              setSelected={setSelected}
            />
             <Item
              title="Reports"
              to="/reportsData"
              icon={<MenuBookIcon />}
              selected={selected}
              setSelected={setSelected}
              
            />
           
            <Item
              title="Monthly Reports"
              to="/monthlyReports"
              icon={<SummarizeIcon />}
              selected={selected}
              setSelected={setSelected}
            />
            <Itemgit 
              title="Library"
              to="/library"
              icon={<CollectionsIcon />}
              selected={selected}
              setSelected={setSelected}
            />
             <Item
              title="Settings"
              to="/settings"
              icon={<SettingsIcon />}
              selected={selected}
              setSelected={setSelected}
            /> */}

          </Box>
        </Menu>
      </ProSidebar>
    </Box>
  );
};

export default Sidebar;
