import { Box, Typography, useTheme } from "@mui/material";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import Button from '@mui/material/Button';
import Fade from '@mui/material/Fade';
import Backdrop from '@mui/material/Backdrop';
import Grid from "@mui/material/Grid";
import TextField from '@mui/material/TextField';
import Modal from '@mui/material/Modal';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
import axios from 'axios'
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import url from "../url"
import React, { useEffect, useState } from 'react'
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import Avatar from '@mui/material/Avatar';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import ClipLoader from "react-spinners/ClipLoader";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  bgcolor: 'beige',
  borderRadius: '10px',
  padding: "0px 40px 40px 40px",
  boxShadow: 24,
};
const color = "black"
const override = {
  display: ' block',
  margin: '0 auto',
}
const Team = () => {
  const [loadingAdd, setLoadingAdd] = useState(false);

  // SnakBar 
  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  const [snackbarType, setsnackbarType] = useState('')
  const [snackbarMsg, setsnackbarMsg] = useState('')
  // Tabs value


  const headers = {
    'Content-Type': 'application/json'
  }
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [hideshowarray, setHideShowArray] = useState(false)
  // Approve 
  const [openAdd, setOpenAdd] = React.useState(false);
  const handleOpenAdd = () => {
    setOpenAdd(true);
  }
  const handleCloseAdd = () => setOpenAdd(false);
  // Update 
  // Submit 
  const submitHandler = async () => {
    setLoadingAdd(true)

    setTimeout(() => {
    axios.post(`${url}api/AppUpdates/create`, {
      // images: selectedFile1,
      added_by: adminId,
      profile_image: adminProfileImg,
      name: adminName,
      notes: ArrayList

    }, { headers }).then(response => {
      console.log(response)
      setOpenAdd(false);
      // setData([...data, response.data]);
      getAllData()
      setsnackbarType("success")
      setsnackbarMsg("App Updates Created Successfully")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoadingAdd(false)
      }, 3000)

    
    })
      .catch(err => {
        console.log(err)
        setsnackbarType("error")
        setsnackbarMsg(err)
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
          setLoadingAdd(false)
        }, 3000)
      })
  }, 3000)
  }

  //Get API Axios
  const [data, setData] = useState([]);

  const getAllData = () => {
    axios.get(`${url}api/AppUpdates/get-all`)
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        setData(response.data.result);
      })
      .catch(error => console.error(`Error:${error}`));

  }
  const [adminId, setAdminId] = useState("");
  const [adminName, setAdminName] = useState("");
  const [adminProfileImg, setAdminProfileImg] = useState("");

  const getAdminLogin = (items) => {
    axios.get(`${url}api/admin/get-admin-by-ID/${items}`
    )
      .then((response) => {
        console.log("response.data.data[0]")
        console.log(response.data)
        // setAdminId()
        setAdminId(response.data.data[0]._id)
        if(response.data.data[0].username===undefined||response.data.data[0].username===null){
          setAdminName("Null")

        }else{
          setAdminName(response.data.data[0].username)
        }
        if(response.data.data[0].username===undefined||response.data.data[0].username===null){
          setAdminProfileImg("Null")
        }else{
          setAdminProfileImg(response.data.data[0].img)
        }
      })
      .catch(error => console.error(`Error:${error}`));
  }

  useEffect(() => {
    getAllData();
    const items = JSON.parse(localStorage.getItem('items'));
    console.log(items)
    getAdminLogin(items);

  }, []);
  const [tempFeature, setTempFeature] = useState();
  const [tempFeatureTitle, setTempFeatureTitle] = useState();
  const [ArrayList, setArrayList] = useState([]);
  const RemoveItem = (arrayData) => {
    setArrayList(oldValues => {
      return oldValues.filter(fruit => fruit !== arrayData)
    })
    console.log(ArrayList)
  }
  const AddFeatures = () => {
    console.log(tempFeature);
    if (tempFeature === '' || tempFeature === null || tempFeature === undefined || tempFeatureTitle === '' || tempFeatureTitle === null || tempFeatureTitle === undefined) {
      console.log('Empty')
    } else {
      console.log('Not Empty')

      if (ArrayList.length === 0) {
        setArrayList([
          {
            title: tempFeatureTitle,
            description: tempFeature
          }
        ])
        setTempFeature('')
        setTempFeatureTitle('')

        setHideShowArray(true)
      } else {
        setArrayList([...ArrayList, 
          {title: tempFeatureTitle,
          description: tempFeature}]);
        setTempFeature('')
        setTempFeatureTitle('')


      }
      console.log(ArrayList)
    }


    //  setArrayList(ArrayList,tempFeature)

  }
  return (
    <>
      <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/home">
            <HomeIcon />
          </Link>

          <Typography color="text.primary">App Updates Releases</Typography>
        </Breadcrumbs>
        <Button variant="contained"
          style={{ backgroundColor: `${colors.grey[400]}` }}
          // color={colors.grey[400]}
          onClick={() => handleOpenAdd()}>
          Add
        </Button>

      </Box>
      <div style={{ height: '80vh', overflowY: 'scroll' }}>
        <Box m="20px">
          <Grid container spacing={2} >
            <Grid item xs={12} md={12} mt>
              <Header title="App Updates" subtitle="Managing the Updates" />

            </Grid>
          </Grid>
          <Box sx={{ width: '100%' }}>
            <Box
              m="5px 0 100px 0px"
              height="70vh"
              sx={{
                "& .MuiDataGrid-root": {
                  border: "none",
                },
                "& .MuiDataGrid-cell": {
                  borderBottom: "none",
                },
                "& .name-column--cell": {
                  color: colors.greenAccent[300],
                },
                "& .MuiDataGrid-columnHeaders": {
                  backgroundColor: colors.grey[400],
                  borderBottom: "none",
                },
                "& .MuiDataGrid-virtualScroller": {
                  backgroundColor: colors.primary[400],
                },
                "& .MuiDataGrid-footerContainer": {
                  borderTop: "none",
                  backgroundColor: colors.grey[400],
                },
                "& .MuiCheckbox-root": {
                  color: `${colors.greenAccent[200]} !important`,
                },
              }}
            >
              <Grid container spacing={2} >
                {data.map((row) => (
                  <Grid item xs={12} md={4} mt>
                    <Card sx={{ maxWidth: 345 }}>
                      <CardHeader
                        avatar={
                          <Avatar src={`${url}${row.profile_image}`}  />
                        }
                        title={`Added by : ${row.name}`}
                        subheader={row.creation_date.slice(0, 10)}
                      />
                      <CardContent>
                        {row.notes.map((row1) => (
                          <Typography variant="body2" color="text.secondary">
                            {row1.title} : {row1.description}
                          </Typography>
                        ))}

                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>

            </Box>
            {/* </TabPanel> */}

          </Box>
          {/* SnackBar  */}
          <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={open} autoHideDuration={3000} onClose={handleClose}>
            <Alert onClose={handleClose} severity={snackbarType}
              sx={{
                width: '100%'
              }}>
              {snackbarMsg}
            </Alert>
          </Snackbar>

          {/* Add  */}
          <div>
            <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              open={openAdd}
              onClose={handleCloseAdd}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 500,
              }}
            >
              <Fade in={openAdd}>
                <Box sx={style}>
                  <Grid container spacing={2} >
                    <Grid item xs={12} md={12} mt={3}>
                      <Typography variant="h3" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                        Add App Release Update
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={2}>
                      {/* color: '#7e7e7e */}
                      <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                        Notes
                      </Typography>
                    </Grid>
                    <Grid item xs={12} md={4}>
                      <TextField
                        value={tempFeatureTitle}
                        placeholder="Enter Title"
                        style={{ width: '100%' }}
                        onChange={(e) => setTempFeatureTitle(e.target.value)
                        }
                        id="filled-basic"
                        variant="filled" />

                    </Grid>
                    <Grid item xs={12} md={4}>
                      <TextField
                        placeholder="Enter Description"
                        value={tempFeature}
                        style={{ width: '100%' }}
                        onChange={(e) => setTempFeature(e.target.value)
                        }
                        id="filled-basic"
                        variant="filled" />

                    </Grid>
                    <Grid item xs={2} md={2}>

                      <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}`, width: '60px', height: '50px' }}
                        onClick={() => AddFeatures()
                        }
                      >
                        Add
                      </Button>
                    </Grid>
                    <Grid item xs={12} md={12} align="left">
                      {hideshowarray ? <>
                        <Grid container spacing={2}>
                          {ArrayList.map((arrayData) => (
                            <>
                              <Grid item xs={1} md={1} >
                                <FiberManualRecordIcon style={{fontSize:'12px'}} />

                              </Grid>
                              <Grid item xs={10} md={10} >
                                <Typography color={colors.grey[300]} variant="h6" gutterBottom>
                                  {arrayData.title} : {arrayData.description}
                                </Typography>
                              </Grid>
                             
                              <Grid item xs={1} md={1} >
                                <RemoveCircleOutlineIcon onClick={() => RemoveItem(arrayData)} style={{ cursor: 'pointer' }} />
                              </Grid>
                            </>
                          ))}

                        </Grid>
                      </> : <></>}


                    </Grid>
                    <Grid item xs={12} md={12} align="center">
                      <Button variant="contained" onClick={() => submitHandler()} style={{ backgroundColor: `${colors.grey[400]}`, width: '100px', height: '50px' }}
                      >
                        {loadingAdd ? <ClipLoader color={color} loading={loadingAdd} css={override} size={30} /> : <h3>Submit</h3>}

                      </Button>
                    </Grid>
                  </Grid>
                </Box>
              </Fade>
            </Modal>
          </div>
        </Box>
      </div>
    </>
  );
};

export default Team;
