import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Grid, useTheme } from '@mui/material'
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import image from '../../components/Images/logo.png'
import DataBg from '../../components/Images/illution.png'
import useWindowSize from "./useWindowSize";
import { Typography } from "@mui/material";

import Avatar from '@mui/material/Avatar';
import url from '../url'
import ClipLoader from "react-spinners/ClipLoader";
import TextField from '@mui/material/TextField';
import { tokens } from "../../theme";
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { useFormik } from 'formik';
import * as yup from 'yup';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const InputStyle = {
  width: '100%',
}

const override = {
  display: ' block',
  margin: '0 auto',
}
const logoStyle = {
  width: '40%',
  height: '50%',
  marginBottom: '10px'
}
const color = "black"

function Login() {
  const [loading, setLoading] = useState("");
  const [loading1, setLoading1] = useState(false);

  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)

    }, 3000)
  }, [])
  //    Get 
  let navigate = useNavigate();
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const headers = {
    'Content-Type': 'application/json'
  }
  // Formik 
  const validationSchema = yup.object({
    email: yup
      .string()
      .email('Invalid email')
      .required('Required Email'),
    password: yup
      .string('Enter your password')
      .required('Password is required')

  });
  const formik = useFormik({
    initialValues: {
      email:'',
      password: '',
    },
    validationSchema: validationSchema,
    onSubmit: (values, { resetForm }) => {
      console.log(values)
       // Loader 
       setLoading1(true)
       setTimeout(async () => {
         await axios.put(`${url}api/admin/login`, {
           email: values.email,
           password: values.password
         }, { headers }).then(response => {
           if (response.data.message === "Login Successfully") {
             console.log('Login Successfully')
             console.log(response.data.data._id)
             localStorage.setItem('items', JSON.stringify(response.data.data._id));
             setsnackbarType("success")
             setsnackbarMsg("Login Successfully")
             setOpen(true);
 
             setTimeout(() => {
               setOpen(false);
               setLoading1(false)
               resetForm('')
               navigate('/home')
             }, 3000)
           } else if (response.data.message === "Email Not Found") {
             setsnackbarType("error")
             setsnackbarMsg("Email Doesnot Exist")
             setOpen(true);
             setTimeout(() => {
               setOpen(false);
               setLoading1(false)
             }, 3000)
           } else if (response.data.message === "Invalid Password") {
             setsnackbarType("error")
             setsnackbarMsg("Invalid Password")
             setOpen(true);
             setTimeout(() => {
               setOpen(false);
               setLoading1(false)
             }, 3000)
           } else {
             setsnackbarType("error")
             setsnackbarMsg("Something Went Wrong")
             setOpen(true);
             setTimeout(() => {
               setOpen(false);
               setLoading1(false)
             }, 3000)
           }
         })
           .catch(err => {
             console.log(err)
             setsnackbarType("error")
             setsnackbarMsg("Something Went Wrong")
             setOpen(true);
             setTimeout(() => {
               setOpen(false);
               setLoading1(false)
             }, 3000)
           })
 
       }, 3000)
    },
  });
  const btn = {
    width: '99%',
    marginTop: '20px',
    // marginBottom: '20px',
    color: `${colors.grey[123]}`,
    backgroundColor: `${colors.grey[111]}`,
    borderColor: '#ada6f2',
    height: '50px',
    padding: '0px',
  }
  const ContainerStyle = {
    padding: '20px',
    paddingTop: '80px',
    height: '110vh',
    backgroundColor: `${colors.grey[124]}`

  }
  // SnakBar 
  const [snackbarType, setsnackbarType] = useState('')
  const [snackbarMsg, setsnackbarMsg] = useState('')

  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  return (
    <div>
      < Grid container spacing={2} style={ContainerStyle}>
        <Grid item xs={12} md={4} >
        </Grid>
        <Grid item xs={12} md={4} >
          <Card sx={{ minWidth: 275, borderRadius: '10px' }}>
            < Grid container spacing={2} >
             
                  <Grid item xs={12} md={12} style={{ padding: '30px' }}>
                  <form onSubmit={formik.handleSubmit} >
                    <Grid align='center' style={{ margin: '20px 10px 0px 10px' }}>
                      <Avatar src={image} variant="square" style={logoStyle} ></Avatar>
                      <TextField style={InputStyle} id="outlined-basic"
                        value={formik.values.email}
                        onChange={(e) => formik.setFieldValue("email", e.target.value)}
                        error={formik.touched.email && Boolean(formik.errors.email)}
                        helperText={formik.touched.email && formik.errors.email}
                        label="Email" variant="outlined" />
                      <br /><br />
                      <TextField style={InputStyle} id="outlined-basic"
                       value={formik.values.password}
                       onChange={(e) => formik.setFieldValue("password", e.target.value)}
                       error={formik.touched.password && Boolean(formik.errors.password)}
                       helperText={formik.touched.password && formik.errors.password}
                        label="Password" variant="outlined" />

                      <br />
                      <Button variant="contained" type="submit"
                      style={btn} >
                        {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={10} /> : <h3>Login</h3>}
                      </Button>

                      <br />
                      <Typography align='right' variant="h6" style={{ color: 'blue', textDecoration: 'underline', cursor: 'pointer' }} onClick={() => {
                        navigate('/forget-password')
                      }} component="h2">
                        Forget Password
                      </Typography>

                    </Grid>
                    </form>
                  </Grid>
              {/* SnackBar  */}
              <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }}  open={open} autoHideDuration={3000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={snackbarType} sx={{ width: '100%' }}>
                  {snackbarMsg}
                </Alert>
              </Snackbar>
            </Grid>
          </Card>
        </Grid>
        <Grid item xs={12} md={2} >
        </Grid>
        <Grid item xs={12} md={12} >
        </Grid>


      </Grid>
    </div>
  )
}

export default Login
