import React, { useState, useEffect } from "react";
import { Routes, Route, useLocation } from "react-router-dom";
import Topbar from "./scenes/global/Topbar";
import Sidebar from "./scenes/global/Sidebar";
import Dashboard from "./scenes/dashboard";
import { CssBaseline, ThemeProvider } from "@mui/material";
import { ColorModeContext, useMode } from "./theme";
import LoginPage from "./scenes/loginPage/login"
import SettingsData from "./scenes/settingsData"
import UsersApp from "./scenes/usersApp"
import ForgetPaswordPage from "./scenes/forgetPassword/forgetPassword"
import SubscriptionPlan from './scenes/subscriptionPlan'
import PrivacyPolicy from './scenes/privacypolicy'
import TermsAndConditions from './scenes/termsAndConditions';
import UsersAppView from './scenes/usersAppView'
import UsersAppUpdates from './scenes/usersAppUpdate'
function App() {
  const [theme, colorMode] = useMode();
  const [isSidebar, setIsSidebar] = useState(true);
  const { pathname } = useLocation();
  useEffect(() => {
    if (window.location.pathname === '/'||window.location.pathname === '/forget-password') {
      setIsSidebar(false)
    }
  }, []);
  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <div className="app" >
          {pathname === '/'||pathname === '/forget-password' ? null :
            <Sidebar isSidebar={isSidebar} />
          }
          <main className="content">
            {pathname === '/'||pathname === '/forget-password' ? null :
              <Topbar setIsSidebar={setIsSidebar} />

            }
            <Routes>

              <Route path="/" element={<LoginPage />} />
              <Route path="/home" element={<Dashboard />} />
              <Route path="/subscriptionPlan" element={<SubscriptionPlan />} />

              <Route path="/privacypolicy" element={<PrivacyPolicy />} />
              <Route path="/termsAndConditions" element={<TermsAndConditions />} />
              <Route path="/usersView" element={<UsersAppView />} />
              <Route path="/appUpdates" element={<UsersAppUpdates />} />

              <Route path="/users" element={<UsersApp />} />
              <Route path="/settings" element={<SettingsData />} />
              <Route path="/forget-password" element={<ForgetPaswordPage />} />

            </Routes>
          </main>
        </div>



    
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
}

export default App;


