import { Box, Typography, useTheme, IconButton } from "@mui/material";
import { tokens } from "../../theme";
import Header from "../../components/Header";
import Button from '@mui/material/Button';
import Fade from '@mui/material/Fade';
import Backdrop from '@mui/material/Backdrop';
import CloseIcon from '@mui/icons-material/Close';
import Grid from "@mui/material/Grid";
import TextField from '@mui/material/TextField';
import Modal from '@mui/material/Modal';
import DoneIcon from '@mui/icons-material/Done';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
import axios from 'axios'
import Swal from 'sweetalert2'
import { useNavigate } from 'react-router-dom';
import Tooltip from '@mui/material/Tooltip';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Badge from '@mui/material/Badge';
import url from "../url"
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';


import {
  DataGrid,
  GridToolbarContainer,
  GridToolbarColumnsButton,
  GridToolbarFilterButton,
  GridToolbarExport,
  GridToolbarDensitySelector,
} from '@mui/x-data-grid';
import React, { useState, useEffect } from "react";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  // width: '100%',
  bgcolor: 'beige',
  borderRadius: '10px',
  padding: "0px 40px 40px 40px",
  boxShadow: 24,
  // p: 4,
};
const style1 = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'beige',
  borderRadius: '10px',
  boxShadow: 24,
  p: 4,
};

const Team = () => {
   // SnakBar 
   const [open, setOpen] = React.useState(false);

   const handleClose = (event, reason) => {
     if (reason === 'clickaway') {
       return;
     }
     setOpen(false);
   };
   const [snackbarType, setsnackbarType] = useState('')
   const [snackbarMsg, setsnackbarMsg] = useState('')
  // Tabs value

  const navigate = useNavigate();

  const headers = {
    'Content-Type': 'application/json'
  }
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [title, setTitle] = useState('');
  const [reportBy, setReportBy] = useState('');
  const [eventCategory, setEventCategory] = useState('');
  const [description, setDescription] = useState('');
  const [location, setLocation] = useState('');
  const [eventDate, setEventDate] = useState('');
  // Approve 
  const [openAdd, setOpenAdd] = React.useState(false);
  const handleCloseAdd = () => setOpenAdd(false);
  // Update 
  const [openUpdate, setOpenUpdate] = React.useState(false);

  const handleOpenUpdate = (row) => {
    navigate('/usersView',
    {
      state: {
        idData: row,
      }
    })
    setOpenUpdate(true);
    console.log(row)
  }
  const handleCloseUpdate = () => setOpenUpdate(false);
  // Delete 
  const [visibleDelete, setVisibleDelete] = useState(false)
  // Delete 
  const deleteData = (id) => {
    console.log('deleting User')
    console.log(id);
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: 'btn btn-success',
        cancelButton: 'btn btn-danger'
      },
      buttonsStyling: {
        backgroundColor: '#4CAF50', /* Green */
        border: 'none',
        color: 'white',
        padding: '15px 32px',
        textAlign: 'center',
        textDecoration: 'none',
        display: 'inline-block',
        fontSize: '16px'
      }
    })
    swalWithBootstrapButtons.fire({
      title: 'Are you sure?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        axios.delete(`${url}api/user/delete/${id}`,
          { headers })
          .then(res => {
            console.log(res);
            console.log(res.data);
            getAllData()
            setsnackbarType("success")
            setsnackbarMsg("User Deleted Successfully !")
            setOpen(true);
            setOpenAdd(false);
            // setOpen1(true);
          }).catch(err => {
            console.log(err)
          })
      } else if (
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire(
          'Cancelled',
          'User is safe :)',
          'error'
        )
      }
    })

  }
  // Submit 
  const submitHandler = async () => {
    axios.post(`${url}create-event`, {
      // id:1,
      // images: selectedFile1,
      title: title,
      description: description,
      location: location,
      date: eventDate,
      category: eventCategory,
      department: reportBy

    }, { headers }).then(response => {
      console.log(response)
      setOpenAdd(false);
      // setData([...data, response.data]);
      getAllData()

      let timerInterval
      Swal.fire({
        title: 'Created Event Successfully',
        timer: 2000,
        timerProgressBar: true,
        didOpen: () => {
          Swal.showLoading()
          const b = Swal.getHtmlContainer().querySelector('b')
          timerInterval = setInterval(() => {
            b.textContent = Swal.getTimerLeft()
          }, 100)
        },
        willClose: () => {
          clearInterval(timerInterval)
        }
      }).then((result) => {
        /* Read more about handling dismissals below */
        if (result.dismiss === Swal.DismissReason.timer) {
          console.log('I was closed by the timer')
          // setSelectedFile1([]);
          setTitle('');
          setDescription('');
          setLocation('');
          setEventDate('');
          setEventCategory('');
          setReportBy('')
        }
      })
    })
      .catch(err => {
        console.log(err)
      })
    // })


  }
  const columns = [
    {
      field: "email",
      headerName: "Email",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            {(row.row.email === undefined) || (row.row.email === null) || (row.row.email === '') ? <>Null</> : <>{row.row.email}</>}
          </>
        )
      }
    },
    {
      field: "verified_status",
      headerName: "Verified Status",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
<Grid container spacing={2} >
                  <Grid item xs={1} md={1} mt></Grid>
                  <Grid item xs={4} md={4} mt>
                  {(row.row.verified_status) ?
              <>
                <Badge color="secondary" badgeContent="Verified">
                </Badge></> : <>
                <Badge color="error" badgeContent="Not Verified">
                </Badge></>
            }
                  </Grid>

    </Grid>
          

          </>

        );
      },
    },
    {
      field: "_id",
      headerName: "Action",
      flex: 1,
      renderCell: (row) => {
        return (
          <>
            {/* <IconButton
            // onClick={() => handleOpenUpdate(row.row._id)}
            >
              <Tooltip title="Edit">
                <EditIcon />
              </Tooltip>
            </IconButton> */}
            <IconButton onClick={() => handleOpenUpdate(row.row._id)}>
              <Tooltip title="View">
                <VisibilityIcon />
              </Tooltip>
            </IconButton>
            <IconButton
             onClick={() => deleteData(row.row._id)}
            >
              <Tooltip title="Delete">

                <DeleteIcon style={{ color: 'red' }} />
              </Tooltip>
            </IconButton>
          </>

        );
      },
    },

  ];
  function CustomToolbar() {
    return (
      <GridToolbarContainer style={{ marginBottom: '5px', backgroundColor: `${colors.primary[600]}` }}>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton />
        <GridToolbarDensitySelector />
        <GridToolbarExport />


      </GridToolbarContainer>
    );
  }
  //Get API Axios
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  const getAllData = () => {
    axios.get(`${url}api/user/get-all`)
      .then((response) => {
        const allData = response.data;
        console.log(allData);
        setData(response.data.result);
        // setimagesdata(response.data.images);
        setLoading(false)
      })
      .catch(error => console.error(`Error:${error}`));

  }

  useEffect(() => {
    getAllData();

  }, []);

  return (
    <>
      <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/home">
            <HomeIcon />
          </Link>

          <Typography color="text.primary">Users</Typography>
        </Breadcrumbs>

      </Box>
      <div style={{height:'550px',overflowY:'scroll'}}>
      <Box m="20px">
        <Grid container spacing={2} >
          <Grid item xs={12} md={12} mt>
            <Header title="Users" subtitle="Managing the Users" />

          </Grid>
        </Grid>
        <Box sx={{ width: '100%' }}>

          <Box
            m="5px 0 100px 0px"
            height="70vh"
            sx={{
              "& .MuiDataGrid-root": {
                border: "none",
              },
              "& .MuiDataGrid-cell": {
                borderBottom: "none",
              },
              "& .name-column--cell": {
                color: colors.greenAccent[300],
              },
              "& .MuiDataGrid-columnHeaders": {
                backgroundColor: colors.grey[400],
                borderBottom: "none",
              },
              "& .MuiDataGrid-virtualScroller": {
                backgroundColor: colors.primary[400],
              },
              "& .MuiDataGrid-footerContainer": {
                borderTop: "none",
                backgroundColor: colors.grey[400],
              },
              "& .MuiCheckbox-root": {
                color: `${colors.greenAccent[200]} !important`,
              },
            }}
          >
            <DataGrid
              // {...data}

              rows={data}
              columns={columns}
              getRowId={(row) => row._id}
              loading={loading}
              components={{
                Toolbar: CustomToolbar,
              }}
            />
          </Box>
          {/* </TabPanel> */}

        </Box>
         {/* SnackBar  */}
         <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={open} autoHideDuration={3000} onClose={handleClose}>
          <Alert onClose={handleClose} severity={snackbarType}
            sx={{
              width: '100%'
            }}>
            {snackbarMsg}
          </Alert>
        </Snackbar>

        {/* Add  */}
        <div>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={openAdd}
            onClose={handleCloseAdd}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={openAdd}>
              <Box sx={style}>
                <Grid container spacing={2} >
                  <Grid item xs={12} md={12} mt={3}>
                    <Typography variant="h3" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                      Add Category
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={3}>
                    {/* color: '#7e7e7e */}
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Name
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>
                    <TextField value={title} style={{ width: '100%' }}
                      onChange={(e) => setTitle(e.target.value)
                      } id="filled-basic" label="Enter Category" variant="filled" />
                  </Grid>
                  <Grid item xs={12} md={12} align="center">
                    {/* <button type='submit'>Submit</button> */}
                    <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}` }} onClick={() => submitHandler()}>
                      Submit
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Fade>
          </Modal>
        </div>
        {/* Update  */}
        <div>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={openUpdate}
            onClose={handleCloseUpdate}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={openUpdate}>
              <Box sx={style}>

                <Grid container spacing={2} >
                  <Grid item xs={12} md={12} mt={3}>
                    <Typography variant="h3" style={{ color: `${colors.grey[400]}`, fontWeight: 700 }} gutterBottom>
                      Edit Category
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={3}>
                    <Typography color={colors.grey[600]} variant="h5" mt={2} style={{ fontWeight: 700 }} gutterBottom>
                      Name
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={9}>

                    <TextField value={title} style={{ width: '100%' }}
                      onChange={(e) => setTitle(e.target.value)
                      } id="filled-basic" label="Enter Category" variant="filled" />
                  </Grid>
                  <Grid item xs={12} md={12} align="center">
                    <Button variant="contained" style={{ backgroundColor: `${colors.grey[400]}` }} onClick={() => submitHandler()}>
                      Update
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Fade>
          </Modal>
        </div>
        {/* Delete  */}
        <div>
          <Modal
            open={visibleDelete}
            onClose={() => setVisibleDelete(false)}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <Box sx={style1}>
              <Grid container spacing={2} align="center">
                <Grid item xs={12} md={12}>
                  <Typography id="modal-modal-title" variant="h6" component="h2">
                    Are you sure you want to delete<br /> this Staff Member?
                  </Typography>
                </Grid>
                <Grid item xs={6} md={6}>
                  <Button startIcon={<DoneIcon />} variant="outlined" onClick={() => setVisibleDelete(false)}>
                    Yes
                  </Button>
                </Grid>
                <Grid item xs={6} md={6}>
                  <Button startIcon={<CloseIcon />} variant="contained" onClick={() => setVisibleDelete(false)}>
                    No
                  </Button>
                </Grid>
              </Grid>
            </Box>
          </Modal>
        </div>
        {/* </Box> */}
      </Box>
      </div>
    </>
  );
};

export default Team;
