import React, { useState, useEffect, useRef } from 'react'
import axios from 'axios';
import { Grid } from '@mui/material'
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom'
import Avatar from '@mui/material/Avatar';
import url from '../url'
import ClipLoader from "react-spinners/ClipLoader";
import TextField from '@mui/material/TextField';
import { useTheme, Typography,Box } from "@mui/material";
import { tokens } from "../../theme";
import Card from '@mui/material/Card';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Badge from '@mui/material/Badge';
import AddIcon from '@mui/icons-material/Add';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import HomeIcon from '@mui/icons-material/Home';
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const ContainerStyle = {
  padding: '10px',
  height: "80vh",
  overflowY:'scroll',
  color: 'white',
}

const InputStyle = {
  width: '100%',
}

const override = {
  display: ' block',
  margin: '0 auto',
  //   borderColor: 'red',
}
const logoStyle = {
  width: '30%',
  height: '30%',
  marginBottom: '10px'
}
const color = "black"

const heading = "Update Password"
function Login() {

  const theme = useTheme();

  const colors = tokens(theme.palette.mode);

  const [items, setItems] = useState([]);

  const gridCont = {
    padding: '30px',
    margin: '20px',
    // marginLeft: '20px',
    // marginRight: '20px',
    // marginTop: '-70px',
    backgroundColor: `${colors.primary[400]}`,
    // height:'430px'
  }

  const getAdminLogin = (items) => {
    axios.get(`${url}api/admin/get-admin-by-ID/${items}`
    )
      .then((response) => {
        console.log("response.data.data[0]")
        console.log(response.data)
        setItems(response.data.data[0]._id)
        setEmail(response.data.data[0].email)
        setUsername(response.data.data[0].username)
        setcompanyLogo(response.data.data[0].img)

        // setPassword(response.data.password)
      })
      .catch(error => console.error(`Error:${error}`));
  }
  useEffect(() => {

    const items = JSON.parse(localStorage.getItem('items'));
    console.log(items)
    setItems(items)
    getAdminLogin(items);

  }, []);
  const [loading, setLoading] = useState("");
  const [loading1, setLoading1] = useState(false);
  const [loading2, setLoading2] = useState(false);


  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)

    }, 3000)
  }, [])
  //    Get 
  let navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");

  const [password, setPassword] = useState("");
  //  const [idData, setidData] = useState("");

  const headers = {
    'Content-Type': 'application/json'
  }
  // SnakBar 
  const [snackbarType, setsnackbarType] = useState('')
  const [snackbarMsg, setsnackbarMsg] = useState('')

  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  const submitHandler = async (e) => {
    e.preventDefault()
    // Loader 
    // setLoading1(true)

    if (email === '' || password === '') {
      setsnackbarType("error")
      setsnackbarMsg("Please Fill All Fields!")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading1(false)
      }, 3000)

    } else {
      setLoading1(true)
      await axios.put(`${url}api/admin/update-credentials`, {
        email: email,
        password: password,
        _id: items
      }, { headers }).then(response => {
        console.log(response)
        setsnackbarType("success")
        setsnackbarMsg("Password Updated Successfully")
        setPassword('')
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
          setLoading1(false)
        }, 3000)

      })
        .catch(err => {
          console.log(err)
          setsnackbarType("error")
          setsnackbarMsg(err)
          setOpen(true);
          setTimeout(() => {
            setOpen(false);
            setLoading1(false)
          }, 3000)
        })
    }

  }
  const submitHandler1 = async (e) => {
    e.preventDefault()
    // Loader 
    setLoading2(true)
if(companyLogo === ''){
  setsnackbarType("error")
  setsnackbarMsg("Please Select Profile Image!")
  setOpen(true);
  setTimeout(() => {
    setOpen(false);
    setLoading2(false)
  }, 3000)
}else if (username === '') {
      setsnackbarType("error")
      setsnackbarMsg("Please Fill Username!")
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setLoading2(false)
      }, 3000)

    } else {
      setLoading2(true)
      await axios.put(`${url}api/admin/update-profile`, {
        username: username,
        img: companyLogo,
        _id: items
      }, { headers }).then(response => {
        console.log(response)
        setsnackbarType("success")
        setsnackbarMsg("Profile Updated Successfully")
        setPassword('')
        setOpen(true);
        setTimeout(() => {
          setOpen(false);
          setLoading2(false)
        }, 3000)

      })
        .catch(err => {
          console.log(err)
          setsnackbarType("error")
          setsnackbarMsg(err)
          setOpen(true);
          setTimeout(() => {
            setOpen(false);
            setLoading1(false)
          }, 3000)
        })
    }

  }
  const btn = {
    width: '99%',
    marginTop: '20px',
    // marginBottom: '20px',
    color: `${colors.grey[123]}`,
    backgroundColor: `${colors.grey[111]}`,
    borderColor: '#ada6f2',
    height: '50px',
    padding: '0px',
  }
  // choose image
  const inputRef = useRef(null);

  const handleClick = () => {
    //  open file input box on click of other element
    inputRef.current.click();
  };
  const [companyLogo, setcompanyLogo] = useState('');
  const onFileChange1 = (e) => {
    const formData = new FormData()
    formData.append('image', e)
    axios.post(`${url}upload-image`,
      formData).then(response => {
        console.log(response.data)
        setcompanyLogo(response.data);
        console.log('resdbb');
        console.log(companyLogo);

      })
  }
  return (
    <div>
       <Box display="flex" justifyContent="space-between" p={2} style={{ borderBottom: '1px solid #adadad' }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/home">
            <HomeIcon />
          </Link>

          <Typography color="text.primary">Settings</Typography>
        </Breadcrumbs>

       
      </Box>
      < Grid container spacing={2} style={ContainerStyle}>

        <Grid item xs={12} md={6}
        >
          <Card sx={{ minWidth: 275 }}>
            < Grid container spacing={2} >
              <Grid item xs={12} md={12} style={gridCont} align='center'>

                <Grid align='center'>
                  {/* <Grid item xs={12} md={12}> */}
                  <Badge color="primary" sx={{ cursor: "pointer" }} onClick={handleClick} overlap="circular" badgeContent={
                    <>
                      <AddIcon />
                      <input type="file" name="image" placeholder="image" style={{ display: 'none' }}
                        ref={inputRef}
                        onChange={(e) => onFileChange1(e.target.files[0])} />
                    </>} >
                    <Avatar src={`${url}${companyLogo}`} sx={{ width: "100px", height: "100px", border: '1px solid white', marginBottom: '10px' }} />

                  </Badge>
                  <Typography variant="h6"
                    color={colors.grey[300]} style={{ fontSize: '20px', margin: '20px', fontWeight: 'bold' }}>Update Admin</Typography>

                  {/* Email  */}

                  <TextField style={InputStyle} id="outlined-basic"
                    value={username}
                    onChange={
                      (e) => setUsername(e.target.value)
                    }
                    label="Username" variant="outlined" required />
                  <br /><br />
                  <TextField style={InputStyle} id="outlined-basic"
                    value={email}
                    disabled
                    onChange={
                      (e) => setEmail(e.target.value)
                    }
                    label="Email" variant="outlined" required />
                  <br />
                  <Button variant="contained" onClick={
                    submitHandler1
                  } style={btn} >
                    {loading2 ? <ClipLoader color={color} loading={loading2} css={override} size={10} /> : <h3>Update</h3>}
                  </Button>
                  <br />
                </Grid>

              </Grid>

            </Grid>
            {/* SnackBar  */}
            <Snackbar open={open} anchorOrigin={{ vertical: 'top', horizontal: 'right' }} autoHideDuration={3000} onClose={handleClose}>
              <Alert onClose={handleClose} severity={snackbarType} sx={{ width: '100%' }}>
                {snackbarMsg}
              </Alert>
            </Snackbar>

          </Card>
        </Grid>
        <Grid item xs={12} md={6}
        >
          <Card sx={{ minWidth: 275 }}>
            < Grid container spacing={2} >
              <Grid item xs={12} md={12} style={gridCont} align='center'>

                <Grid align='center'>
                 
                  <Typography variant="h6"
                    color={colors.grey[300]} style={{ fontSize: '20px', margin: '20px', fontWeight: 'bold' }}>Update Password</Typography>
                  <br /><br />
                  <TextField style={InputStyle} id="outlined-basic"
                    value={email}
                    disabled
                    onChange={
                      (e) => setEmail(e.target.value)
                    }
                    label="Email" variant="outlined" required />
                  <br /><br />
                  <TextField style={InputStyle} id="outlined-basic"
                    value={password}

                    onChange={
                      (e) => setPassword(e.target.value)
                    }
                    label="Password" variant="outlined" required />

                  <br />
                  <Button variant="contained" onClick={
                    submitHandler
                  } style={btn} >
                    {loading1 ? <ClipLoader color={color} loading={loading1} css={override} size={10} /> : <h3>Update</h3>}
                  </Button>
                  <br />
                </Grid>

              </Grid>

            </Grid>
            {/* SnackBar  */}
            <Snackbar open={open} anchorOrigin={{ vertical: 'top', horizontal: 'right' }} autoHideDuration={3000} onClose={handleClose}>
              <Alert onClose={handleClose} severity={snackbarType} sx={{ width: '100%' }}>
                {snackbarMsg}
              </Alert>
            </Snackbar>

          </Card>
        </Grid>
      </Grid></div>
  )
}

export default Login
